import { createHttpEffect } from '@servicenow/ui-effect-http';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import {
	contextHelpers,
	validationBehavior
} from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
import styles from './styles.scss';
import '@servicenow/now-textarea';
import '@servicenow/now-pill';
import { WIZARD_PAGES, ACTIONS } from '../../constants';
import '../../../components/sn-spoke-name';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: [
		'gmail_user_id',
		'gmail_from',
		'gmail_to',
		'gmail_email_body',
		'gmail_connection'
	]
});

const { GMAIL_ACTION_SETUP_PAGE, ACTION_SETUP_PAGE } = WIZARD_PAGES;
const {
	GUIDED_WIZARD_CONTINUE,
	GUIDED_WIZARD_PREVIOUS,
	GUIDED_WIZARD_CANCEL,
	STEPPER_BEHAVIOR_PAGE_SET,
	FETCH_CONNECTIONS,
	FETCH_CONNECTIONS_SUCCEEDED,
	FETCH_CONNECTIONS_FAILED
} = ACTIONS;

const {
	helpers: { createValidationBehavior, returnFormValidState }
} = validationBehavior;

const dataParam = {
	start: '0',
	count: '1000000',
	table: 'sys_connection',
	qualifier: '',
	sysparm_for_impersonation: false,
	p: 'sys_connection;q:connection_aliasSTARTSWITHsn_gmail_spoke.Gmail'
};

export const requestConnectionCredentials = createHttpEffect(
	'/angular.do?sysparm_type=ref_list_data',
	{
		method: 'POST',
		successActionType: FETCH_CONNECTIONS_SUCCEEDED,
		errorActionType: FETCH_CONNECTIONS_FAILED,
		batch: false,
		dataParam: 'data'
	}
);

createCustomElement(GMAIL_ACTION_SETUP_PAGE, {
	view: (state, dispatch) => {
		const {
			connectionCredentials,
			gmail_user_id,
			gmail_from,
			gmail_to,
			gmail_email_body,
			gmail_connection,
			isFormValid,
			properties: { wizardContext }
		} = state;

		return (
			<sn-guided-wizard-page
				primaryDisabled={!isFormValid}
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_CONTINUE, {
						...contextFromState,
						error: null
					});
				}}
				onPrevious={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_PREVIOUS, {
						...contextFromState,
						error: null
					});
				}}
				onSecondaryClick={() => dispatch(GUIDED_WIZARD_CANCEL)}
				onClose={wizardContext.onClose}
				runningHeaderText={t('BUILD A FLOW FROM A TEMPLATE')}
				maxWidth={wizardContext.maxWidth}>
				<sn-spoke-name
					spokeName={t('Gmail')}
					spokeAction={t('Send Email')}
					icon={wizardContext.selectedSpokeIcon}
				/>
				<div className="flow-action-form-fields">
					<now-select
						label={t('Connection')}
						append-to-payload={{ field: 'gmail_connection' }}
						selected-item={gmail_connection}
						items={connectionCredentials}
						search="contains"
						variant="tertiary"
						placeholder={t('Select Connection & Credential Alias')}
						style={{ width: '100%' }}
					/>
					<now-input
						type="text"
						label={t('User ID')}
						placeholder={t('Enter user ID')}
						style={{ width: '100%' }}
						value={gmail_user_id}
						name="gmail_user_id"
						required={true}
						append-to-payload={{ field: 'gmail_user_id' }}
					/>
					<now-input
						type="email"
						label={t('From')}
						placeholder={t('Enter sender email address')}
						style={{ width: '100%' }}
						value={gmail_from}
						required={true}
						name="gmail_from"
						append-to-payload={{ field: 'gmail_from' }}
					/>
					<now-input
						type="email"
						label={t('To')}
						placeholder={t('Enter recipient email address')}
						style={{ width: '100%' }}
						value={gmail_to}
						required={true}
						name="gmail_to"
						append-to-payload={{ field: 'gmail_to' }}
					/>
					<now-textarea
						type="text"
						label={t('Message')}
						placeholder={t('Enter message')}
						style={{ width: '100%' }}
						value={gmail_email_body}
						rows="3"
						append-to-payload={{ field: 'gmail_email_body' }}
					/>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		connectionCredentials: [],
		gmail_user_id: '',
		gmail_from: '',
		gmail_to: '',
		gmail_email_body: '',
		gmail_connection: '',
		isFormValid: false
	},
	properties: {
		wizardContext: { default: null },
		connectionsData: {
			default: []
		}
	},
	transformState: state => {
		const isFormValid = returnFormValidState(state);
		return { ...state, isFormValid };
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			dispatch(STEPPER_BEHAVIOR_PAGE_SET, {
				page: ACTION_SETUP_PAGE
			});

			const initState = getStateFromContext(wizardContext);
			updateState(initState);

			dispatch(FETCH_CONNECTIONS, { data: dataParam });
		},
		['NOW_INPUT#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		},
		['NOW_TEXTAREA#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		},
		[FETCH_CONNECTIONS]: requestConnectionCredentials,
		[FETCH_CONNECTIONS_SUCCEEDED]: ({ action: { payload }, updateState }) => {
			let crendentials = [];
			let selected_gmail = '';
			if (payload && payload.items && payload.items.length > 0) {
				crendentials = payload.items.map(item => {
					if (item.active == 'true') {
						selected_gmail = item.sys_id;
					}
					return {
						id: item.sys_id,
						label: item.$$displayValue
					};
				});
			}
			updateState({
				connectionCredentials: crendentials,
				gmail_connection: selected_gmail
			});
		},

		[FETCH_CONNECTIONS_FAILED]: ({ action: { payload }, dispatch }) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		}
	},
	behaviors: [
		createValidationBehavior(['gmail_user_id', 'gmail_from', 'gmail_to'])
	],
	styles
});
