import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-loader';
import { contextHelpers } from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
import 'sn-app-object-wizard-components';
import '../../../components/sn-flow-connection-warning';
import { WIZARD_PAGES, ACTIONS, SPOKE_IDS } from '../../constants';
import infinityIcon from '../../../assets/infinity-icon.png';
import styles from './styles.scss';
import spokesDataBehavior from '../../../behaviors/spokes-data';

const notificationSources = [
	{
		id: SPOKE_IDS.SLACK,
		name: 'slack',
		label: t('Slack')
	},
	{
		id: SPOKE_IDS.TEAMS,
		name: 'teams',
		label: t('Microsoft Teams')
	},
	{
		id: SPOKE_IDS.EMAIL,
		label: t('Email'),
		name: 'email',
		icon: '/images/icon-global-scope.svg',
		isDisabled: false
	},
	{
		id: SPOKE_IDS.GMAIL,
		name: 'gmail',
		label: t('Gmail')
	},
	{
		id: SPOKE_IDS.TWILIO,
		name: 'twilio',
		label: t('Twilio')
	}
];

const { SPOKE_PAGE } = WIZARD_PAGES;
const {
	GUIDED_WIZARD_CONTINUE,
	GUIDED_WIZARD_PREVIOUS,
	GUIDED_WIZARD_CANCEL,
	STEPPER_BEHAVIOR_PAGE_SET
} = ACTIONS;

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['spoke_selection']
});

const {
	constants: {
		INSTALLED_SPOKES_PROPERTY,
		MISSING_CONNECTIONS_PROPERTY,
		SPOKES_DATA_LOADING_PROPERTY
	},
	helpers: { fetchSpokes }
} = spokesDataBehavior;

function resolveSpokesProperty(installedSpokes) {
	notificationSources.forEach(source => {
		if (source.id !== SPOKE_IDS.EMAIL) {
			let index = installedSpokes.findIndex(spoke => spoke.scope === source.id);
			source.isDisabled = index === -1 ? true : false;
			source.icon = index === -1 ? infinityIcon : installedSpokes[index].logo;
		}
	});
}

createCustomElement(SPOKE_PAGE, {
	view: (state, dispatch) => {
		const {
			spoke_selection,
			selectedSpokeIcon,
			properties: {
				wizardContext,
				[SPOKES_DATA_LOADING_PROPERTY]: spokesLoading,
				[INSTALLED_SPOKES_PROPERTY]: installedSpokes,
				[MISSING_CONNECTIONS_PROPERTY]: missingConnections
			}
		} = state;

		if (!spokesLoading) {
			resolveSpokesProperty(installedSpokes);
		}

		return (
			<sn-guided-wizard-page
				primaryDisabled={!spoke_selection}
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_CONTINUE, {
						...contextFromState,
						selectedSpokeIcon,
						error: null
					});
				}}
				onPrevious={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_PREVIOUS, {
						...contextFromState,
						error: null
					});
				}}
				onSecondaryClick={() => dispatch(GUIDED_WIZARD_CANCEL)}
				onClose={wizardContext.onClose}
				runningHeaderText={t('BUILD A FLOW FROM A TEMPLATE')}
				headerText={t('Select a spoke for your template')}
				subheaderText={t(
					'Spokes let you connect to external system and applications'
				)}
				maxWidth={wizardContext.maxWidth}>
				{spokesLoading ? (
					<now-loader />
				) : (
					<div className="spokes-container">
						<sn-flow-connection-warning
							connections={missingConnections}
							installed-spokes={installedSpokes}
						/>
						<div className="tile-buttons-container">
							{notificationSources.map(source => {
								return (
									<sn-aow-tile-button
										icon={source.icon}
										className="flow-card"
										id={source.name}
										text={source.label}
										disabled={source.isDisabled}
										isSelected={spoke_selection === source.name}
										appendToPayload={{ icon: source.icon }}
										helperContent={
											source.isDisabled
												? t('Learn about why this is disabled')
												: null
										}
									/>
								);
							})}
						</div>
					</div>
				)}
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		spoke_selection: '',
		selectedSpokeIcon: ''
	},
	properties: {
		wizardContext: { default: null },
		[INSTALLED_SPOKES_PROPERTY]: { default: [] },
		[MISSING_CONNECTIONS_PROPERTY]: { default: [] },
		[SPOKES_DATA_LOADING_PROPERTY]: { default: true }
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			const {
				app: { sys_id },
				templateId
			} = wizardContext;
			if (sys_id && templateId) {
				fetchSpokes(dispatch, sys_id, templateId);
			}
			dispatch(STEPPER_BEHAVIOR_PAGE_SET, {
				page: SPOKE_PAGE
			});

			const initState = getStateFromContext(wizardContext);
			if (wizardContext.selectedSpokeIcon) {
				initState.selectedSpokeIcon = wizardContext.selectedSpokeIcon;
			}
			updateState(initState);
		},
		['AOW_TILE_BUTTON#CLICKED']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			if (payload && payload.id) {
				stopPropagation();
				updateState({
					spoke_selection: payload.id,
					selectedSpokeIcon: payload.icon
				});
			}
		}
	},
	behaviors: [{ behavior: spokesDataBehavior.behavior }],
	styles
});
