import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	BEHAVIOR_NAME,
	UNINSTALLED_SPOKES_PROPERTY,
	INSTALLED_SPOKES_PROPERTY,
	MISSING_CONNECTIONS_PROPERTY,
	SPOKES_DATA_LOADING_PROPERTY,
	DEFAULT_QUERY_PARAMS,
	SPOKES_DATA_BEHAVIOR__FETCH,
	SPOKES_DATA_BEHAVIOR__FETCH_SPOKES,
	SPOKES_DATA_BEHAVIOR__FETCH_TEMPLATE,
	SPOKES_DATA_BEHAVIOR__FETCH_TEMPLATE_SUCCEEDED,
	SPOKES_DATA_BEHAVIOR__FETCH_SPOKES_STARTED,
	SPOKES_DATA_BEHAVIOR__FETCH_SPOKES_SUCCEEDED,
	SPOKES_DATA_BEHAVIOR__FETCH_SPOKES_FAILED,
	DEPENDENCY_RULE_TYPE
} from './constants';

export default {
	name: BEHAVIOR_NAME,

	properties: {
		[UNINSTALLED_SPOKES_PROPERTY]: { default: [] },
		[MISSING_CONNECTIONS_PROPERTY]: { default: [] },
		[SPOKES_DATA_LOADING_PROPERTY]: { default: true }
	},

	actionHandlers: {
		[SPOKES_DATA_BEHAVIOR__FETCH]: ({
			action: {
				payload: { templateId, sysparm_transaction_scope },
				type
			},
			dispatch
		}) => {
			if (!templateId || !sysparm_transaction_scope) {
				throw new Error(
					`[${BEHAVIOR_NAME}] '${type}' requires that you pass the current app 'sysparm_transaction_scope' and 'templateId' in the payload`
				);
			}
			dispatch(SPOKES_DATA_BEHAVIOR__FETCH_TEMPLATE, {
				templateId,
				sysparm_transaction_scope,
				style: 'lite'
			});
		},

		[SPOKES_DATA_BEHAVIOR__FETCH_TEMPLATE]: createHttpEffect(
			'/api/now/templates/:templateId',
			{
				method: 'GET',
				startActionType: SPOKES_DATA_BEHAVIOR__FETCH_SPOKES_STARTED,
				successActionType: SPOKES_DATA_BEHAVIOR__FETCH_TEMPLATE_SUCCEEDED,
				errorActionType: SPOKES_DATA_BEHAVIOR__FETCH_SPOKES_FAILED,
				batch: false,
				pathParams: 'templateId',
				queryParams: ['style', 'sysparm_transaction_scope']
			}
		),

		[SPOKES_DATA_BEHAVIOR__FETCH_TEMPLATE_SUCCEEDED]: ({
			action: { payload },
			dispatch,
			updateProperties
		}) => {
			const { dependency_evaluation_results } = payload.result;
			const uninstalledSpokes = [];
			const installedSpokes = [];
			const missingConnections = [];

			dependency_evaluation_results.forEach(dep => {
				let scopeName = '';
				let spokeDisplayValue = '';
				if (dep.type === DEPENDENCY_RULE_TYPE.TEMPLATE_SPOKE_INSTALLED) {
					scopeName = dep.dependency.inputs.scope_name;
					dep.checkFailed
						? uninstalledSpokes.push(scopeName)
						: installedSpokes.push(scopeName);
				}
				if (dep.type === DEPENDENCY_RULE_TYPE.CONNECTION_CONFIGURED) {
					spokeDisplayValue = dep.dependency.inputs.spoke_name;
					if (dep.checkFailed) {
						missingConnections.push({
							spokeName: spokeDisplayValue,
							scopeName: dep.dependency.inputs.scope_name
						});
					}
				}
			});

			updateProperties({
				[UNINSTALLED_SPOKES_PROPERTY]: uninstalledSpokes,
				[MISSING_CONNECTIONS_PROPERTY]: missingConnections
			});

			if (installedSpokes.length > 0) {
				const queryParams = {
					...DEFAULT_QUERY_PARAMS,
					scope_name: installedSpokes.join(',')
				};
				dispatch(SPOKES_DATA_BEHAVIOR__FETCH_SPOKES, queryParams);
			} else {
				updateProperties({
					[SPOKES_DATA_LOADING_PROPERTY]: false
				});
			}
		},

		[SPOKES_DATA_BEHAVIOR__FETCH_SPOKES]: createHttpEffect(
			'/api/now/template_spokes_details',
			{
				method: 'GET',
				batch: false,
				queryParams: Object.keys(DEFAULT_QUERY_PARAMS),
				successActionType: SPOKES_DATA_BEHAVIOR__FETCH_SPOKES_SUCCEEDED,
				errorActionType: SPOKES_DATA_BEHAVIOR__FETCH_SPOKES_FAILED
			}
		),

		[SPOKES_DATA_BEHAVIOR__FETCH_SPOKES_STARTED]: ({ updateProperties }) => {
			updateProperties({ [SPOKES_DATA_LOADING_PROPERTY]: true });
		},

		[SPOKES_DATA_BEHAVIOR__FETCH_SPOKES_SUCCEEDED]: ({
			action,
			updateProperties
		}) => {
			const { result } = action.payload;
			updateProperties({
				[INSTALLED_SPOKES_PROPERTY]: result,
				[SPOKES_DATA_LOADING_PROPERTY]: false
			});
		},

		[SPOKES_DATA_BEHAVIOR__FETCH_SPOKES_FAILED]: ({
			action,
			updateProperties
		}) => {
			updateProperties({
				[SPOKES_DATA_LOADING_PROPERTY]: false
			});
			throw new Error(`${BEHAVIOR_NAME} ${action.type} failed to fetch`);
		}
	}
};
