import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-label-value';

createCustomElement('sn-spoke-name', {
	view: ({ properties: { spokeName, spokeAction, icon } }) => {
		return (
			<div>
				<span>
					<img
						src={icon}
						alt={`${spokeName} ${spokeAction} icon`}
						style={{
							width: '30px',
							height: '30px',
							float: 'left',
							'margin-right': '10px'
						}}
					/>
				</span>
				<span>
					<now-label-value-stacked
						items={[
							{
								label: spokeName,
								value: { type: 'string', value: spokeAction }
							}
						]}
						itemMinWidth=""
						delimiter=","
						size="md"
						align="horizontal"
					/>
				</span>
			</div>
		);
	},
	properties: {
		spokeName: { default: '' },
		spokeAction: { default: '' },
		icon: { default: '' }
	}
});
