// UI-Framework imports
import { createCustomElement } from '@servicenow/ui-core';
// sn-guided-wizard-toolkit components imports
import { getCurrentPage, stepperBehavior } from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
// Pages
import './pages/sn-zoom-trigger-select-wizard-page';
import './pages/sn-zoom-spoke-select-wizard-page';
import './pages/sn-zoom-actions-wizard-page';
import './pages/sn-define-zoom-flow-wizard-page';
import '../components/sn-flow-summary-wizard-page';

import { WIZARD_PAGES } from './constants';

// styles
import styles from './styles.scss';
import { FLOW_PAGE_TEXT } from '../helpers/flow-logic-naming-constants';

const {
	TRIGGER_PAGE,
	SPOKE_PAGE,
	SETUP_ZOOM_ACTIONS_PAGE,
	SUMMARY_PAGE,
	DEFINE_FLOW_PAGE
} = WIZARD_PAGES;

var STEPPER_CONFIG = {
	[TRIGGER_PAGE]: t('Define trigger'),
	[SPOKE_PAGE]: t('Select spoke'),
	[SETUP_ZOOM_ACTIONS_PAGE]: t('Set up actions'),
	[DEFINE_FLOW_PAGE]: FLOW_PAGE_TEXT,
	[SUMMARY_PAGE]: t('Summary')
};

// Create flowDefinition, assign continue/previous for each page and define startPage
const fdZoom = {
	startPage: TRIGGER_PAGE,
	pages: {
		[TRIGGER_PAGE]: {
			continue: () => SPOKE_PAGE
		},
		[SPOKE_PAGE]: {
			continue: () => SETUP_ZOOM_ACTIONS_PAGE,
			previous: () => TRIGGER_PAGE
		},
		[SETUP_ZOOM_ACTIONS_PAGE]: {
			continue: () => DEFINE_FLOW_PAGE,
			previous: () => SPOKE_PAGE
		},
		[DEFINE_FLOW_PAGE]: {
			continue: () => SUMMARY_PAGE,
			previous: () => SETUP_ZOOM_ACTIONS_PAGE
		},
		[SUMMARY_PAGE]: {
			previous: () => DEFINE_FLOW_PAGE
		}
	}
};

// Create wizard element
// This will be the element that is called from the template
// Make sure to include this in now-ui.json with innerComponents
createCustomElement('sn-zoom-flow-wizard', {
	view: ({ properties }, dispatch) => {
		// Create element from current page set in sn-guided-wizard
		// Note: Must be capitalized in order for the variable component to render
		const { wizardContext, stepperItems, templateId, app } = properties;
		const PageComponent = getCurrentPage(wizardContext);
		return (
			<sn-guided-wizard
				// Always set a component-id
				component-id="fdZoomWizard"
				// Flow definition from above
				flow-definition={fdZoom}
				// Stepper items from behavior
				stepper-items={stepperItems}
				tables-data-options={{
					normalizeResult: result => {
						return result.map(table => {
							return {
								id: table.name,
								label: `${table.label} [${table.name}]`,
								is_extendable: table.is_extendable
							};
						});
					}
				}}
				// Define initial context
				// Not required but it's nice to define what forms you may
				// have to fill out during a wizard here and set any recurring values
				// here like maxWidth
				initial-context={{
					maxWidth: '620px',
					onClose: () => dispatch('SN_GUIDED_WIZARD#CANCEL'),
					templateId,
					app
				}}>
				{/** Render current page and be sure to provide it wizardContext */}
				<PageComponent wizard-context={wizardContext} />
			</sn-guided-wizard>
		);
	},
	properties: {
		// wizardContext is the main data store for a wizard, always include
		// this property selector in the root of a wizard
		wizardContext: { default: '@fdZoomWizard/wizardContext' },
		// stepperItems property selector populated by the stepperBehavior
		stepperItems: { default: '@fdZoomWizard/stepperItems' },
		templateId: { default: '735e6341fff2101088e94ee9453bf1a6' }, // Create a new row in a spreadsheet from a record template
		app: { default: {} }
	},
	behaviors: [
		{ behavior: stepperBehavior.behavior, options: { config: STEPPER_CONFIG } }
	],
	actionHandlers: {},
	styles
});
