import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import '@servicenow/now-input';
import '../../../components/sn-spoke-name';
import '../../../components/sn-flow-connection-warning';
import infinityIcon from '../../../assets/infinity-icon.png';
import spokesDataBehavior from '../../../behaviors/spokes-data';

import styles from '../../styles.scss';

import { contextHelpers } from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
import {
	SN_GW__REQUEST_CONNECTION_CREDENTIALS,
	SN_GW__GET_CONNECTION_CREDENTIALS_SUCCEEDED,
	SN_GW__GET_CONNECTION_CREDENTIALS_FAILED,
	dataParam
} from './constants';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['jira_connection_credential_alias']
});

export const requestConnectionCredentials = createHttpEffect(
	'/angular.do?sysparm_type=ref_list_data',
	{
		method: 'POST',
		successActionType: SN_GW__GET_CONNECTION_CREDENTIALS_SUCCEEDED,
		errorActionType: SN_GW__GET_CONNECTION_CREDENTIALS_FAILED,
		batch: false,
		dataParam: 'data'
	}
);

const {
	helpers: { fetchSpokes },
	constants: {
		INSTALLED_SPOKES_PROPERTY,
		MISSING_CONNECTIONS_PROPERTY,
		SPOKES_DATA_LOADING_PROPERTY
	}
} = spokesDataBehavior;

function renderSpokeNameIcon(installedSpokes) {
	if (installedSpokes.length === 0) {
		return null;
	}

	const icon = installedSpokes[0].logo ? installedSpokes[0].logo : infinityIcon;
	return (
		<div>
			<sn-spoke-name
				spokeName={t('Jira')}
				spokeAction={t('Create Issue')}
				icon={icon}
			/>
		</div>
	);
}

createCustomElement('sn-jira-issue-actions-wizard-page', {
	view: (state, { dispatch }) => {
		const {
			connectionCredentials,
			jira_connection_credential_alias,
			properties: {
				wizardContext,
				[SPOKES_DATA_LOADING_PROPERTY]: spokesLoading,
				[INSTALLED_SPOKES_PROPERTY]: installedSpokes,
				[MISSING_CONNECTIONS_PROPERTY]: missingConnections
			}
		} = state;

		return (
			<sn-guided-wizard-page
				max-width={wizardContext.maxWidth}
				running-header-text={t('BUILD A FLOW FROM TEMPLATE')}
				header-text={t("Let's define what happens when your flow runs")}
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#CONTINUE', {
						...contextFromState,
						error: null
					});
				}}
				onSecondaryClick={() => {
					// Can pass in a newContext on the previous action as well
					const newContext = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#PREVIOUS', newContext);
				}}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onclose={wizardContext.onClose}
				primary-label="Continue"
				primary-disabled={!state.connectionCredentials}>
				{spokesLoading ? (
					<now-loader />
				) : (
					<Fragment>
						{renderSpokeNameIcon(installedSpokes)}
						<sn-flow-connection-warning
							connections={missingConnections}
							installed-spokes={installedSpokes}
						/>
						<div className="inputs">
							<now-select
								label={t('Connection & Credentials Alias')}
								append-to-payload={{ name: 'jira_connection_credential_alias' }}
								items={connectionCredentials}
								search="contains"
								selectedItem={jira_connection_credential_alias}
								variant="tertiary"
								select="single"
								placeholder={t('Select Connection & Credential Alias')}
								style={{ width: '100%' }}
							/>
						</div>
					</Fragment>
				)}
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {},
		[INSTALLED_SPOKES_PROPERTY]: { default: [] },
		[MISSING_CONNECTIONS_PROPERTY]: { default: [] },
		[SPOKES_DATA_LOADING_PROPERTY]: { default: true }
	},
	initialState: {
		jira_connection_credential_alias: '',
		credentialsLoaded: false,
		connectionCredentials: []
	},
	transformState: state => {
		return { ...state };
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			dispatch,
			properties: { wizardContext },
			updateState
		}) => {
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-jira-issue-actions-wizard-page'
			});
			const initState = getStateFromContext(wizardContext);
			updateState(initState);
			dispatch(SN_GW__REQUEST_CONNECTION_CREDENTIALS, { data: dataParam });
			const {
				app: { sys_id },
				templateId
			} = wizardContext;
			if (sys_id && templateId) {
				fetchSpokes(dispatch, sys_id, templateId);
			}
		},
		[SN_GW__REQUEST_CONNECTION_CREDENTIALS]: requestConnectionCredentials,
		[SN_GW__GET_CONNECTION_CREDENTIALS_SUCCEEDED]: ({
			action: { payload },
			updateState
		}) => {
			let crendentials = [];
			if (payload && payload.items && payload.items.length > 0) {
				crendentials = payload.items.map(item => {
					return {
						id: item.sys_id,
						label: item.$$displayValue
					};
				});
			}
			updateState({
				connectionCredentials: crendentials,
				credentialsLoaded: true
			});
		},
		[SN_GW__GET_CONNECTION_CREDENTIALS_FAILED]: ({
			action: { payload },
			dispatch
		}) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		}
	},
	behaviors: [{ behavior: spokesDataBehavior.behavior }],
	styles
});
