import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-input';
import '@servicenow/now-alert';
import { t } from 'sn-translate';
import { contextHelpers } from '@devsnc/guided-wizard-toolkit';
import 'sn-app-object-wizard-components';

import '../../../components/sn-flow-connection-warning';
import infinityIcon from '../../../assets/infinity-icon.png';
import spokesDataBehavior from '../../../behaviors/spokes-data';
import styles from './styles.scss';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['spoke_selection']
});

const {
	constants: {
		INSTALLED_SPOKES_PROPERTY,
		MISSING_CONNECTIONS_PROPERTY,
		SPOKES_DATA_LOADING_PROPERTY
	},
	helpers: { fetchSpokes }
} = spokesDataBehavior;

function resolveSpokesProperty(notificationSources, installedSpokes) {
	notificationSources.forEach(source => {
		if (source.id !== 'email') {
			let index = installedSpokes.findIndex(spoke => spoke.scope === source.id);
			source.isDisabled = index === -1 ? true : false;
			source.icon = index === -1 ? infinityIcon : installedSpokes[index].logo;
		}
	});
}

createCustomElement('sn-reminder-spoke-select-wizard-page', {
	view: (state, { dispatch }) => {
		const {
			spoke_selection,
			selectedSpokeIcon,
			properties: {
				wizardContext,
				[SPOKES_DATA_LOADING_PROPERTY]: spokesLoading,
				[INSTALLED_SPOKES_PROPERTY]: installedSpokes,
				[MISSING_CONNECTIONS_PROPERTY]: missingConnections
			}
		} = state;

		const notificationSources = [
			{
				id: 'sn_slack_ah_v2',
				name: 'slack',
				label: t('Slack')
			},
			{
				id: 'sn_ms_teams_ah',
				name: 'teams',
				label: t('Microsoft Teams')
			},
			{
				id: 'sn_gmail_spoke',
				name: 'gmail',
				label: t('Gmail')
			},
			{
				id: 'sn_twilio_spoke',
				name: 'twilio',
				label: t('Twilio')
			}
		];

		if (!spokesLoading) {
			resolveSpokesProperty(notificationSources, installedSpokes);
		}

		return (
			<sn-guided-wizard-page
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#CONTINUE', {
						...contextFromState,
						selectedSpokeIcon,
						error: null
					});
				}}
				primaryDisabled={!spoke_selection}
				onClose={wizardContext.onClose}
				// onPrevious - presence will render the close button (iconic button, top left)
				onPrevious={() => dispatch('SN_GUIDED_WIZARD#PREVIOUS')}
				running-header-text={t('BUILD A FLOW FROM TEMPLATE')}
				max-width={wizardContext.maxWidth}
				header-text={t('Select a spoke for your template')}
				subheader-text={t(
					'Spokes let you connect to external system and applications'
				)}>
				{spokesLoading ? (
					<now-loader />
				) : (
					<Fragment>
						<sn-flow-connection-warning
							connections={missingConnections}
							installed-spokes={installedSpokes}
						/>
						<div className="body-container">
							{notificationSources.map(spokeSource => {
								return (
									<sn-aow-tile-button
										icon={spokeSource.icon}
										className="flow-spokes-btn"
										id={spokeSource.name}
										text={spokeSource.label}
										isSelected={spoke_selection === spokeSource.name}
										disabled={spokeSource.isDisabled}
										appendToPayload={{ icon: spokeSource.icon }}
										helperContent={
											spokeSource.isDisabled
												? t('Learn about why this is disabled')
												: null
										}
									/>
								);
							})}
						</div>
					</Fragment>
				)}
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {},
		[INSTALLED_SPOKES_PROPERTY]: { default: [] },
		[MISSING_CONNECTIONS_PROPERTY]: { default: [] },
		[SPOKES_DATA_LOADING_PROPERTY]: { default: true }
	},
	initialState: {
		spoke_selection: '',
		selectedSpokeIcon: ''
	},
	transformState: state => {
		return { ...state };
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			dispatch,
			properties: { wizardContext },
			updateState
		}) => {
			const {
				app: { sys_id },
				templateId
			} = wizardContext;
			if (sys_id && templateId) {
				fetchSpokes(dispatch, sys_id, templateId);
			}

			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-reminder-spoke-select-wizard-page'
			});
			const initState = getStateFromContext(wizardContext);
			if (wizardContext.selectedSpokeIcon) {
				initState.selectedSpokeIcon = wizardContext.selectedSpokeIcon;
			}
			updateState(initState);
		},
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			// We're relying on the `name` property of the `now-input` matching directly
			// to the key on state here so this is the default format for actions dispatched
			// by DS primitives
			const { value, name } = payload;
			updateState({ [name]: value });
			// Expect some kind of abstraction here in the future
		},
		['AOW_TILE_BUTTON#CLICKED']: ({
			action: {
				stopPropagation,
				payload: { id, icon }
			},
			updateState
		}) => {
			stopPropagation();
			updateState({
				spoke_selection: id,
				selectedSpokeIcon: icon
			});
		}
	},
	behaviors: [{ behavior: spokesDataBehavior.behavior }],
	styles
});
