import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-input';
import '@servicenow/now-collapse';
import '@servicenow/now-icon';
import '@servicenow/now-pill';
import '@servicenow/now-typeahead';
import '@servicenow/now-select';
import {
	validationBehavior,
	contextHelpers
} from '@devsnc/guided-wizard-toolkit';
import { createHttpEffect } from '@servicenow/ui-effect-http';
const {
	helpers: { createValidationBehavior, returnFormValidState }
} = validationBehavior;
import styles from './styles.scss';

import {
	SN_GW__REQUEST_FLOW_ROLES,
	SN_GW__GET_FLOW_ROLES_SUCCEEDED,
	SN_GW__GET_FLOW_ROLES_FAILED,
	dataParam
} from './constants';

import {
	HEADER_TEXT,
	NAME_PLACEHOLDER_TEXT,
	RUN_TEXT
} from '../../../helpers/flow-logic-naming-constants';
import { generateStyleToolTipContent } from '../../../helpers/sn-naming-helpers';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: [
		'flow_name',
		'flow_description',
		'application',
		'flow_protection',
		'flow_run_as',
		'selectedRoles',
		'flow_run_with_roles'
	]
});

export const requestFlowRoles = createHttpEffect(
	'/angular.do?sysparm_type=ref_list_data',
	{
		method: 'POST',
		successActionType: SN_GW__GET_FLOW_ROLES_SUCCEEDED,
		errorActionType: SN_GW__GET_FLOW_ROLES_FAILED,
		batch: false,
		dataParam: 'data'
	}
);

createCustomElement('sn-approval-define-flow-wizard-page', {
	view: (state, { dispatch }) => {
		const {
			flow_name,
			flow_description,
			isFormValid,
			isAdvancedExpanded,
			application,
			flow_protection,
			flow_run_as,
			selectedRoles,
			allRoles,
			clickedRole,
			properties: { wizardContext }
		} = state;
		function onPrimaryClick() {
			const contextFromState = getContextFromState(state, wizardContext);
			dispatch('SN_GUIDED_WIZARD#CONTINUE', {
				...contextFromState,
				error: null
			});
		}

		function renderRolePills() {
			const pills = selectedRoles.map((role, index) => {
				return (
					<now-pill
						append-to-payload={{ role, index }}
						icon="user-outline"
						size="sm"
						tabIndex="-1"
						className="now-pill"
						label={role}
						can-dismiss
					/>
				);
			});
			return <div>{pills}</div>;
		}

		return (
			<sn-guided-wizard-page
				onPrimaryClick={onPrimaryClick}
				primary-disabled={!isFormValid}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				running-header-text={t('BUILD A FLOW FROM TEMPLATE')}
				header-text={HEADER_TEXT}
				subheader-text={t(
					'This flow needs a name, description, and other details.'
				)}
				max-width={wizardContext.maxWidth}>
				<div className="inputs">
					<now-input
						name="flow_name"
						value={flow_name}
						placeholder={NAME_PLACEHOLDER_TEXT}
						label={t('Name')}
						helperContent={generateStyleToolTipContent(
							t(
								'Choose a name that briefly describes what your flow does. Only users with access to Flow Designer can see this name.'
							)
						)}
						size="lg"
						autocomplete="off"
						required
					/>
					<now-input
						name="flow_description"
						value={flow_description}
						placeholder={t('Describe your flow')}
						label={t('Description')}
						helperContent={generateStyleToolTipContent(
							t(
								'Describe how your flow automates work for your app. Only users with access to Flow Designer can see this description.'
							)
						)}
						size="lg"
						autocomplete="off"
					/>
					<br />
					<a
						on-click={event => {
							event.preventDefault();
							dispatch('TOGGLE_ADVANCED_OPTIONS', { isAdvancedExpanded });
						}}>
						{t('Show advanced options')}
						{isAdvancedExpanded ? (
							<now-icon icon="chevron-up-fill" size="lg" />
						) : (
							<now-icon icon="chevron-down-fill" size="lg" />
						)}
					</a>
					<now-collapse expanded={isAdvancedExpanded}>
						<div className="inputs">
							<now-input
								name="application"
								value={application}
								placeholder={t('Loading...')}
								label={t('Application')}
								size="lg"
								autocomplete="off"
								disabled
							/>
							<br />
							<now-select
								label={t('Protection')}
								append-to-payload={{ name: 'flow_protection' }}
								items={[
									{ id: '-- None --', label: t('-- None --') },
									{ id: 'Read-only', label: t('Read-only') }
								]}
								selectedItem={flow_protection}
								icon=""
								placeholder=""
								size="md"
								variant="tertiary"
								select="single"
								helperContent={generateStyleToolTipContent(
									t(
										'Choose whether other users with Flow Designer access can edit your flow (No protection), or if your flow should be read-only.'
									)
								)}
								panelFitProps={{}}
								configAria={{}}
								style={{ width: '100%' }}
							/>
							<br />
							<now-select
								label={RUN_TEXT}
								append-to-payload={{ name: 'flow_run_as' }}
								items={[
									{
										id: 'User who initiates session',
										label: t('User who initiates session')
									},
									{ id: 'System User', label: t('System User') }
								]}
								selectedItem={flow_run_as}
								icon=""
								placeholder=""
								size="md"
								variant="tertiary"
								select="single"
								helperContent={generateStyleToolTipContent(
									t(
										'If you want to temporarily elevate the permissions of the user who triggers the flow, select System User. Selecting System User is less secure but can prevent permissions errors that cause your flow to stop running.'
									)
								)}
								panelFitProps={{}}
								configAria={{}}
								style={{ width: '100%' }}
							/>
							<br />
							{flow_run_as === 'System User' ? (
								''
							) : (
								<div>
									<now-select
										label={t('Run with roles')}
										append-to-payload={{ name: 'roles' }}
										className="roles"
										items={allRoles}
										search="contains"
										selectedItem={clickedRole}
										variant="tertiary"
										helperContent={generateStyleToolTipContent(
											t(
												'Select roles that you want to temporarily assign to the user who triggers your flow.'
											)
										)}
										placeholder={t('Search for a role')}
										style={{ width: '100%' }}
									/>
									<div style={{ display: 'flex' }}>
										{selectedRoles.length > 0 ? renderRolePills() : ''}
									</div>
								</div>
							)}
						</div>
					</now-collapse>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		flow_name: '',
		flow_description: '',
		isAdvancedExpanded: false,
		application: t('Loading...'),
		flow_protection: '-- None --',
		flow_run_as: 'User who initiates session',
		flow_run_with_roles: '',
		selectedRoles: [],
		allRoles: [],
		isFormValid: false,
		clickedRole: ''
	},
	transformState: state => {
		const { selectedRoles } = state;
		const isFormValid = returnFormValidState(state);
		const flow_run_with_roles = selectedRoles.join(',');
		return { ...state, isFormValid, flow_run_with_roles };
	},
	properties: {
		wizardContext: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			// Send to the current page to the stepper behavior
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-approval-define-flow-wizard-page'
			});
			dispatch(SN_GW__REQUEST_FLOW_ROLES, { data: dataParam });

			// Populate state with wizardContext so inputs are persisted when
			// coming back to this page
			let initState = getStateFromContext(wizardContext);
			if (initState.flow_run_with_roles) {
				initState = {
					...initState,
					selectedRoles: initState.flow_run_with_roles.split(',')
				};
			}
			initState = { ...initState, application: wizardContext.app.scope };
			updateState(initState);
		},
		[SN_GW__REQUEST_FLOW_ROLES]: requestFlowRoles,
		[SN_GW__GET_FLOW_ROLES_SUCCEEDED]: ({
			action: { payload },
			updateState
		}) => {
			let rolesNames = [];
			if (payload && payload.items && payload.items.length > 0) {
				rolesNames = payload.items.map(item => {
					return {
						id: item.name,
						label: item.name
					};
				});
			}
			updateState({ allRoles: rolesNames });
		},
		[SN_GW__GET_FLOW_ROLES_FAILED]: ({ action: { payload }, dispatch }) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		},
		// Actions from now-input
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		},
		['TOGGLE_ADVANCED_OPTIONS']: ({ action: { payload }, updateState }) => {
			const { isAdvancedExpanded } = payload;
			updateState({ isAdvancedExpanded: !isAdvancedExpanded });
		},
		'NOW_PILL#DISMISSED': ({
			action: {
				payload: { index }
			},
			state,
			updateState
		}) => {
			const newState = [...state.selectedRoles];
			newState.splice(index, 1);
			updateState({ selectedRoles: newState });
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action: {
				payload: { item, name, value },
				stopPropagation
			},
			state,
			updateState
		}) => {
			switch (name) {
				case 'roles': {
					// no role duplicates
					if (state.selectedRoles.includes(value)) {
						return;
					}
					updateState({
						path: 'selectedRoles',
						value: value,
						operation: 'push'
					});
					break;
				}
				default:
					updateState({ [name]: item.id });
					break;
			}
			stopPropagation();
		}
	},
	behaviors: [createValidationBehavior(['flow_name'])],
	styles
});
