import { createCustomElement, actionTypes } from '@servicenow/ui-core';

import '@servicenow/now-input';
import '@servicenow/now-text-link';
import {
	contextHelpers,
	validationBehavior
} from '@devsnc/guided-wizard-toolkit';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';
import {
	SN_GW__REQUEST_CAT_ITEMS,
	SN_GW__GET_CAT_ITEMS_SUCCEEDED,
	SN_GW__GET_CAT_ITEMS_FAILED,
	dataParam,
	URLDOC,
	page
} from './constants';
import styles from './styles.scss';
import {
	generateStyleToolTipContent,
	TOOLTIP_STYLING
} from '../../../helpers/sn-naming-helpers';

const {
	helpers: { createValidationBehavior }
} = validationBehavior;

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['catalog_variables', 'price']
});

export const requestCatItems = createHttpEffect(
	'/angular.do?sysparm_type=ref_list_data',
	{
		method: 'POST',
		successActionType: SN_GW__GET_CAT_ITEMS_SUCCEEDED,
		errorActionType: SN_GW__GET_CAT_ITEMS_FAILED,
		batch: false,
		dataParam: 'data'
	}
);

createCustomElement('sn-approval-action-setup-wizard-page', {
	view: (state, { dispatch }) => {
		const {
			catItems,
			price,
			catalog_variables,
			fetchCatItemComplete,
			properties: { wizardContext }
		} = state;
		return (
			<sn-guided-wizard-page
				max-width={wizardContext.maxWidth}
				running-header-text={t('BUILD A FLOW FROM TEMPLATE')}
				header-text={t("Let's define what happens when your flow runs")}
				primary-disabled={!fetchCatItemComplete}
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#CONTINUE', {
						...contextFromState,
						error: null
					});
				}}
				onclose={wizardContext.onClose}
				primary-label="Done">
				<br />
				<div className="inputs">
					<now-select
						label={t('Template catalog item')}
						append-to-payload={{ name: 'catalog_variables' }}
						selectedItem={catalog_variables}
						items={catItems}
						search="contains"
						select="single"
						variant="tertiary"
						placeholder={t('Select template catalog item')}
						style={{ width: '100%' }}
						helperContent={
							<div style={TOOLTIP_STYLING}>
								{t(
									'Choose a catalog item whose variables you can use as inputs for automated actions in your flow. You can select the variables that you  want to use in Flow Designer. '
								)}
								<now-text-link
									label={t('Learn More')}
									href={`${URLDOC}/${page}`}
									variant="secondary"
									opens-window={true}
								/>
							</div>
						}
					/>
					<span>
						<now-input
							label={t(
								"Ask for approval if the catalog item's price is greater than"
							)}
							type="number"
							name="price"
							value={price}
							size="lg"
							autocomplete="off"
							helperContent={generateStyleToolTipContent(
								t(
									'Enter a price that, when exceeded, will automatically require approval for the request. You can select who you want to approve the  request in Flow Designer.'
								)
							)}
							style={{ width: '100%' }}
						/>
					</span>
				</div>
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {}
	},
	initialState: {
		isFormValid: false,
		catItems: [],
		price: '',
		selectedCatItem: '',
		fetchCatItemComplete: false
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			dispatch,
			properties: { wizardContext },
			updateState
		}) => {
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-approval-action-setup-wizard-page'
			});
			const initState = getStateFromContext(wizardContext);
			updateState(initState);
			dispatch(SN_GW__REQUEST_CAT_ITEMS, { data: dataParam });
		},
		[SN_GW__REQUEST_CAT_ITEMS]: requestCatItems,
		[SN_GW__GET_CAT_ITEMS_SUCCEEDED]: ({
			action: { payload },
			updateState
		}) => {
			let cat_items = [];
			if (payload && payload.items && payload.items.length > 0) {
				cat_items = payload.items.map(item => {
					return {
						id: item.sys_id,
						label: item.$$displayValue
					};
				});
			}
			updateState({
				fetchCatItemComplete: true,
				catItems: cat_items
			});
		},
		[SN_GW__GET_CAT_ITEMS_FAILED]: ({ action: { payload }, dispatch }) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		},
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action: {
				payload: { item, name },
				stopPropagation
			},
			updateState
		}) => {
			stopPropagation();
			updateState({ [name]: item.id });
		}
	},
	behaviors: [createValidationBehavior(['price'])],
	styles
});
