import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import {
	tablesDataBehavior,
	contextHelpers
} from '@devsnc/guided-wizard-toolkit';
import '@servicenow/now-select';
import '@servicenow/now-loader';
import { t } from 'sn-translate';
import { WIZARD_PAGES, ACTIONS, COMPONENT_ID } from '../../constants';

let {
	constants: { TABLES_DATA_LOADING_PROPERTY, TABLES_DATA_PROPERTY }
} = tablesDataBehavior;

const { TRIGGER_PAGE } = WIZARD_PAGES;
const {
	GUIDED_WIZARD_CONTINUE,
	GUIDED_WIZARD_CANCEL,
	STEPPER_BEHAVIOR_PAGE_SET
} = ACTIONS;

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['trigger_table']
});

createCustomElement(TRIGGER_PAGE, {
	view: (state, dispatch) => {
		let { trigger_table, properties: props } = state;
		let { wizardContext, tables, tablesLoading } = props;

		return (
			<sn-guided-wizard-page
				primaryDisabled={!trigger_table}
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_CONTINUE, {
						...contextFromState,
						error: null
					});
				}}
				onSecondaryClick={() => dispatch(GUIDED_WIZARD_CANCEL)}
				onClose={wizardContext.onClose}
				runningHeaderText={t('BUILD A FLOW FROM A TEMPLATE')}
				headerText={t('Choose a table to trigger your flow')}
				subheaderText={t(
					'When a ServiceNow record is created, Send a notification to a workspace like Slack, Microsoft Teams, Gmail, or SMS.'
				)}
				maxWidth={wizardContext.maxWidth}>
				<div className="inputs">
					{!tablesLoading && tables.length > 0 ? (
						<now-select
							search="contains"
							label={t('Table')}
							append-to-payload={{ name: 'trigger_table' }}
							items={tables}
							selectedItem={trigger_table}
							icon=""
							placeholder=""
							size="md"
							variant="tertiary"
							select="single"
							panelFitProps={{}}
							configAria={{}}
							style={{ width: '100%' }}
						/>
					) : (
						<now-loader />
					)}
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		trigger_table: ''
	},
	properties: {
		wizardContext: { default: null },
		tables: {
			default: `@${COMPONENT_ID}/${TABLES_DATA_PROPERTY}`
		},
		tablesLoading: {
			default: `@${COMPONENT_ID}/${TABLES_DATA_LOADING_PROPERTY}`
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			dispatch(STEPPER_BEHAVIOR_PAGE_SET, {
				page: TRIGGER_PAGE
			});

			const initState = getStateFromContext(wizardContext);
			updateState(initState);
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		}
	}
});
