import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	contextHelpers,
	validationBehavior
} from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
import styles from './styles.scss';
import '@servicenow/now-textarea';
import { WIZARD_PAGES, ACTIONS } from '../../constants';
import '../../../components/sn-spoke-name';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['sms_from', 'sms_to', 'sms_text_message', 'twilio_connection']
});

const { TWILIO_ACTION_SETUP_PAGE, ACTION_SETUP_PAGE } = WIZARD_PAGES;
const {
	GUIDED_WIZARD_CONTINUE,
	GUIDED_WIZARD_PREVIOUS,
	GUIDED_WIZARD_CANCEL,
	STEPPER_BEHAVIOR_PAGE_SET,
	FETCH_TWILIO_CONNECTIONS,
	FETCH_TWILIO_CONNECTIONS_SUCCEEDED,
	FETCH_TWILIO_CONNECTIONS_FAILED
} = ACTIONS;

const {
	helpers: { createValidationBehavior, returnFormValidState }
} = validationBehavior;

export const dataParam = {
	start: '0',
	count: '1000000',
	table: 'sys_connection',
	qualifier: '',
	sysparm_for_impersonation: false,
	p: 'sys_connection;q:connection_aliasSTARTSWITHsn_twilio_spoke.Twilio_Base;'
};

createCustomElement(TWILIO_ACTION_SETUP_PAGE, {
	view: (state, dispatch) => {
		const {
			isFormValid,
			sms_text_message,
			sms_from,
			sms_to,
			connectionCredentials,
			twilio_connection,
			properties: { wizardContext }
		} = state;

		return (
			<sn-guided-wizard-page
				primaryDisabled={!isFormValid}
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_CONTINUE, {
						...contextFromState,
						error: null
					});
				}}
				onPrevious={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_PREVIOUS, {
						...contextFromState,
						error: null
					});
				}}
				onSecondaryClick={() => dispatch(GUIDED_WIZARD_CANCEL)}
				onClose={wizardContext.onClose}
				runningHeaderText={t('BUILD A FLOW FROM A TEMPLATE')}
				max-width={wizardContext.maxWidth}>
				<sn-spoke-name
					spokeName={t('Twilio')}
					spokeAction={t('Send SMS message')}
					icon={wizardContext.selectedSpokeIcon}
				/>
				<div className="flow-action-form-fields">
					<now-select
						label={t('Connection')}
						append-to-payload={{ name: 'twilio_connection' }}
						selectedItem={twilio_connection}
						items={connectionCredentials}
						search="contains"
						variant="tertiary"
						placeholder={t('Select Connection & Credential Alias')}
						style={{ width: '100%' }}
					/>
				</div>
				<div className="flow-action-form-fields">
					<now-input
						type="text"
						label={t('From')}
						value={sms_from}
						name="sms_from"
						required={true}
						style={{ width: '100%' }}
						append-to-payload={{ field: 'sms_from' }}
					/>
				</div>
				<div className="flow-action-form-fields">
					<now-input
						type="text"
						label={t('To')}
						value={sms_to}
						name="sms_to"
						required={true}
						style={{ width: '100%' }}
						append-to-payload={{ field: 'sms_to' }}
					/>
				</div>

				<div className="flow-action-form-fields">
					<now-textarea
						type="text"
						label={t('Message')}
						style={{ width: '100%' }}
						value={sms_text_message}
						name="sms_text_message"
						rows="3"
						required={true}
						append-to-payload={{ field: 'sms_text_message' }}
					/>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		sms_from: '',
		sms_to: '',
		sms_text_message: '',
		isFormValid: false,
		connectionCredentials: [],
		twilio_connection: ''
	},
	properties: {
		wizardContext: { default: null }
	},
	transformState: state => {
		const isFormValid = returnFormValidState(state);
		return { ...state, isFormValid };
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			dispatch(STEPPER_BEHAVIOR_PAGE_SET, {
				page: ACTION_SETUP_PAGE
			});

			const initState = getStateFromContext(wizardContext);
			updateState(initState);

			dispatch(FETCH_TWILIO_CONNECTIONS, { data: dataParam });
		},
		['NOW_INPUT#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		},
		['NOW_TEXTAREA#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action: {
				payload: { item, name },
				stopPropagation
			},
			updateState
		}) => {
			stopPropagation();
			updateState({ [name]: item.id });
		},
		[FETCH_TWILIO_CONNECTIONS]: createHttpEffect(
			'/angular.do?sysparm_type=ref_list_data',
			{
				method: 'POST',
				batch: false,
				dataParam: 'data',
				successActionType: FETCH_TWILIO_CONNECTIONS_SUCCEEDED,
				errorActionType: FETCH_TWILIO_CONNECTIONS_FAILED
			}
		),
		[FETCH_TWILIO_CONNECTIONS_SUCCEEDED]: ({
			action: { payload },
			updateState
		}) => {
			let crendentials = [];
			let selected_connection = '';
			if (payload && payload.items && payload.items.length > 0) {
				crendentials = payload.items.map(item => {
					if (item.active == 'true') {
						selected_connection = item.sys_id;
					}
					return {
						id: item.sys_id,
						label: item.$$displayValue
					};
				});
			}

			updateState({
				connectionCredentials: crendentials,
				twilio_connection: selected_connection
			});
		},

		[FETCH_TWILIO_CONNECTIONS_FAILED]: ({ action: { payload }, dispatch }) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		}
	},
	behaviors: [
		createValidationBehavior(['sms_from', 'sms_to', 'sms_text_message'])
	],
	styles
});
