import { createCustomElement } from '@servicenow/ui-core';
import { getCurrentPage, stepperBehavior } from '@devsnc/guided-wizard-toolkit';
import {
	WIZARD_PAGES,
	STEPPER_CONFIG,
	TEMPLATE_DEF_ID,
	COMPONENT_ID,
	ACTIONS
} from './constants';

import './pages/sn-notif-trigger-select-wizard-page';
import './pages/sn-notif-spoke-select-wizard-page';
import './pages/sn-notif-email-action-setup-wizard-page';
import './pages/sn-notif-slack-action-setup-wizard-page';
import './pages/sn-notif-teams-action-setup-wizard-page';
import './pages/sn-notif-gmail-action-setup-wizard-page';
import './pages/sn-notif-twilio-action-setup-wizard-page';
import './pages/sn-notif-define-flow-wizard-page';
import '../components/sn-flow-summary-wizard-page';

const {
	TRIGGER_PAGE,
	SPOKE_PAGE,
	EMAIL_ACTION_SETUP_PAGE,
	SLACK_ACTION_SETUP_PAGE,
	TEAMS_ACTION_SETUP_PAGE,
	GMAIL_ACTION_SETUP_PAGE,
	TWILIO_ACTION_SETUP_PAGE,
	DEFINE_FLOW_PAGE,
	SUMMARY_PAGE
} = WIZARD_PAGES;

const pageMap = {
	['email']: EMAIL_ACTION_SETUP_PAGE,
	['slack']: SLACK_ACTION_SETUP_PAGE,
	['teams']: TEAMS_ACTION_SETUP_PAGE,
	['gmail']: GMAIL_ACTION_SETUP_PAGE,
	['twilio']: TWILIO_ACTION_SETUP_PAGE
};

const flowDefinition = {
	startPage: TRIGGER_PAGE,
	pages: {
		[TRIGGER_PAGE]: {
			continue: () => SPOKE_PAGE
		},
		[SPOKE_PAGE]: {
			continue: data => pageMap[data.appData['spoke_selection']],
			previous: () => TRIGGER_PAGE
		},
		[EMAIL_ACTION_SETUP_PAGE]: {
			continue: () => DEFINE_FLOW_PAGE,
			previous: () => SPOKE_PAGE
		},
		[SLACK_ACTION_SETUP_PAGE]: {
			continue: () => DEFINE_FLOW_PAGE,
			previous: () => SPOKE_PAGE
		},
		[TEAMS_ACTION_SETUP_PAGE]: {
			continue: () => DEFINE_FLOW_PAGE,
			previous: () => SPOKE_PAGE
		},
		[GMAIL_ACTION_SETUP_PAGE]: {
			continue: () => DEFINE_FLOW_PAGE,
			previous: () => SPOKE_PAGE
		},
		[TWILIO_ACTION_SETUP_PAGE]: {
			continue: () => DEFINE_FLOW_PAGE,
			previous: () => SPOKE_PAGE
		},
		[DEFINE_FLOW_PAGE]: {
			continue: () => SUMMARY_PAGE,
			previous: data => pageMap[data.appData['spoke_selection']]
		},
		[SUMMARY_PAGE]: {
			previous: () => DEFINE_FLOW_PAGE
		}
	}
};

createCustomElement('sn-send-notif-wizard', {
	view: ({ properties }, dispatch) => {
		const { wizardContext, stepperItems, templateId, app } = properties;
		const PageComponent = getCurrentPage(wizardContext);

		return (
			<sn-guided-wizard
				flow-definition={flowDefinition}
				stepper-items={stepperItems}
				component-id={COMPONENT_ID}
				tables-data-options={{
					fetchOnBootstrap: true,
					appId: app.sys_id,
					normalizeResult: result => {
						return result.map(table => {
							return {
								id: table.name,
								label: `${table.label} [${table.name}]`,
								is_extendable: table.is_extendable
							};
						});
					}
				}}
				initial-context={{
					maxWidth: '620px',
					onClose: () => dispatch(ACTIONS.GUIDED_WIZARD_CANCEL),
					templateId,
					appData: {
						flow_scope_sys_id: app.sys_id
					},
					app
				}}>
				<PageComponent wizard-context={wizardContext} />
			</sn-guided-wizard>
		);
	},
	properties: {
		wizardContext: {
			default: `@${COMPONENT_ID}/wizardContext`
		},
		templateId: {
			default: TEMPLATE_DEF_ID
		},
		app: {
			default: {}
		}
	},
	behaviors: [
		{ behavior: stepperBehavior.behavior, options: { config: STEPPER_CONFIG } }
	]
});
