import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-input';
import { runTemplateBehavior } from '@devsnc/guided-wizard-toolkit';
import 'sn-app-object-wizard-components';

import { t } from 'sn-translate';

import styles from './styles.scss';

import flowIcon from '../../assets/icon-flow-refresh.svg';
import progressPagePeople from '../../assets/progress-page-people.png';

import { SN_FLOW_WIZARD__RUN_TEMPLATE } from './constants';

import { RUNNING_HEADER_TEXT } from '../../helpers/flow-logic-naming-constants';

const {
	helpers: { runTemplate },
	actionTypes: {
		RUN_TEMPLATE_SUCCESS,
		RUN_TEMPLATE_FAILURE,
		RUN_TEMPLATE_WARNING
	}
} = runTemplateBehavior;

createCustomElement('sn-flow-summary-wizard-page', {
	view: (
		{
			properties: {
				wizardContext: { onClose, templateId, appData }
			},
			flowSysId,
			loading
		},
		{ dispatch }
	) => {
		function onPrimaryClick() {
			dispatch('SN_GUIDED_WIZARD#COMPLETE', {
				key: 'notification',
				result: appData
			});
		}

		function onSecondaryClick() {
			let flowRecord = { ...appData, sys_id: flowSysId };
			dispatch('SN_GUIDED_WIZARD#EDIT_RECORD', {
				key: 'sys_hub_flow',
				result: flowRecord
			});
		}

		const data = [
			{
				icon: flowIcon,
				label: t('Icon of flow'),
				name: t('Flow icon')
			},
			{
				label: t('Flow name'),
				text: appData.flow_name,
				maxCharLength: 22
			},
			{
				label: t('Description'),
				text: appData.flow_description,
				maxCharLength: 38
			}
		];

		const empty_flow_data = [
			{
				icon: flowIcon,
				label: t('Icon of flow'),
				name: t('Flow icon')
			},
			{
				label: t('Flow name'),
				text: appData.new_flow_name,
				maxCharLength: 22
			},
			{
				label: t('Description'),
				text: appData.description,
				maxCharLength: 38
			}
		];

		return (
			<sn-guided-wizard-page
				onPrimaryClick={onPrimaryClick}
				primaryLabel={t('Done')}
				secondaryLabel={t('Edit this flow')}
				onSecondaryClick={onSecondaryClick}
				onClose={onClose}
				running-header-text={
					templateId === 'b42032c2ff12101088e94ee9453bf1ed'
						? RUNNING_HEADER_TEXT
						: t('BUILD A FLOW FROM TEMPLATE')
				}
				header-text={t('Success! Your flow is ready.')}
				subheader-text={t(
					'You can edit your new flow or choose "Done" to continue building the rest of your app'
				)}
				loading={loading}
				loadingContent={
					templateId === 'b42032c2ff12101088e94ee9453bf1ed'
						? {
								runningHeaderText: RUNNING_HEADER_TEXT,
								headerText: t('Sit tight while we create your flow'),
								subheaderText: t('This may take a few seconds...'),
								image: progressPagePeople
						  }
						: {
								runningHeaderText: t('BUILD A FLOW FROM TEMPLATE'),
								headerText: t('Sit tight while we create your flow'),
								subheaderText: t('This may take a few seconds...'),
								image: progressPagePeople
						  }
				}
				maxWidth="680px">
				<sn-aow-info-summary
					data={
						templateId === 'b42032c2ff12101088e94ee9453bf1ed'
							? empty_flow_data
							: data
					}
				/>
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {}
	},
	initialState: {
		loading: true,
		flowSysId: ''
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ state, properties, dispatch }) => {
			dispatch('STEPPER_BEHAVIOR#COMPLETE_ALL');

			const { wizardContext } = properties;
			let appData = [];
			if (!state.completed && !wizardContext.error) {
				appData = wizardContext.appData;
			}
			dispatch(SN_FLOW_WIZARD__RUN_TEMPLATE, { appData });
		},
		[SN_FLOW_WIZARD__RUN_TEMPLATE]: ({
			action: {
				payload: { appData }
			},
			state,
			dispatch
		}) => {
			const { properties: props } = state;
			const {
				wizardContext: { templateId }
			} = props;
			runTemplate(dispatch, templateId, appData);
		},
		[RUN_TEMPLATE_FAILURE]: ({
			action: { payload },
			dispatch,
			updateState
		}) => {
			updateState({ loading: false });
			dispatch('SN_GUIDED_WIZARD#PREVIOUS', { error: { ...payload } });
		},
		[RUN_TEMPLATE_WARNING]: ({
			action: { payload },
			dispatch,
			updateState
		}) => {
			updateState({ loading: false });
			dispatch('SN_GUIDED_WIZARD#PREVIOUS', { error: { ...payload } });
		},
		[RUN_TEMPLATE_SUCCESS]: ({ action: { payload }, updateState }) => {
			const outputs = payload.result.outputs;

			if (!outputs) {
				console.warn('No output for application found');
				return;
			}

			let flowSysIdOutput = outputs.filter(output => output.name === 'output');
			let flowSysId =
				flowSysIdOutput && flowSysIdOutput.length > 0
					? flowSysIdOutput[0].value
					: '';
			updateState({ loading: false, flowSysId });
		},
		['RUN_TEMPLATE_BEHAVIOR#TEMPLATE_FAILURE']: ({
			action: { payload },
			dispatch,
			updateState
		}) => {
			updateState({ loading: false });
			dispatch('SN_GUIDED_WIZARD#PREVIOUS', { error: { ...payload } });
		}
	},
	behaviors: [runTemplateBehavior.behavior],
	styles
});
