import { createCustomElement, actionTypes } from '@servicenow/ui-core';

import '@servicenow/now-input';
import '@servicenow/now-label-value';
import '@servicenow/now-textarea';
import {
	contextHelpers,
	validationBehavior
} from '@devsnc/guided-wizard-toolkit';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';
import '../../../components/sn-spoke-name';
import {
	SN_GW__REQUEST_CONNECTION_CREDENTIALS,
	SN_GW__GET_CONNECTION_CREDENTIALS_SUCCEEDED,
	SN_GW__GET_CONNECTION_CREDENTIALS_FAILED,
	dataParam
} from './constants';
import styles from './styles.scss';

const {
	helpers: { createValidationBehavior, returnFormValidState }
} = validationBehavior;

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['sms_message', 'sms_to', 'sms_from', 'twilio_connection']
});

export const requestConnectionCredentials = createHttpEffect(
	'/angular.do?sysparm_type=ref_list_data',
	{
		method: 'POST',
		successActionType: SN_GW__GET_CONNECTION_CREDENTIALS_SUCCEEDED,
		errorActionType: SN_GW__GET_CONNECTION_CREDENTIALS_FAILED,
		batch: false,
		dataParam: 'data'
	}
);

createCustomElement('sn-reminder-sms-action-setup-wizard-page', {
	view: (state, { dispatch }) => {
		const {
			isFormValid,
			connectionCredentials,
			sms_to,
			sms_from,
			sms_message,
			twilio_connection,
			properties: { wizardContext }
		} = state;
		return (
			<sn-guided-wizard-page
				max-width={wizardContext.maxWidth}
				running-header-text={t('BUILD A FLOW FROM TEMPLATE')}
				header-text={t("Let's define what happens when your flow runs")}
				primary-disabled={!isFormValid}
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#CONTINUE', {
						...contextFromState,
						error: null
					});
				}}
				onPrevious={() => {
					// Can pass in a newContext on the previous action as well
					const newContext = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#PREVIOUS', newContext);
				}}
				onclose={wizardContext.onClose}
				primary-label="Done">
				<sn-spoke-name
					spokeName={t('Twilio')}
					spokeAction={t('Send an SMS message')}
					icon={wizardContext.selectedSpokeIcon}
				/>
				<div className="inputs">
					<now-select
						label={t('Connection')}
						append-to-payload={{ name: 'twilio_connection' }}
						items={connectionCredentials}
						selectedItem={twilio_connection}
						search="contains"
						variant="tertiary"
						placeholder={t('Select Connection & Credential Alias')}
						style={{ width: '100%' }}
					/>
					<now-input
						name="sms_to"
						type="number"
						append-to-payload={{ field: 'sms_to' }}
						value={sms_to}
						label={t('To')}
						size="lg"
						autocomplete="off"
						style={{ width: '100%' }}
						required={true}
					/>
					<now-input
						name="sms_from"
						type="number"
						append-to-payload={{ field: 'sms_from' }}
						value={sms_from}
						label={t('From')}
						size="lg"
						autocomplete="off"
						style={{ width: '100%' }}
						required={true}
					/>

					<div className="flow-action-form-fields">
						<now-textarea
							type="text"
							label={t('Message')}
							style={{ width: '100%' }}
							value={sms_message}
							name="sms_message"
							resize="none"
							rows="5"
							required={true}
							append-to-payload={{ field: 'sms_message' }}
						/>
					</div>
				</div>
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {}
	},
	initialState: {
		isFormValid: false,
		connectionCredentials: [],
		sms_to: '',
		sms_from: '',
		sms_message: '',
		twilio_connection: '',
		fetchCredentialsComplete: false
	},
	transformState: state => {
		const isFormValid = returnFormValidState(state);
		return { ...state, isFormValid };
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			dispatch,
			properties: { wizardContext },
			updateState
		}) => {
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-reminder-action-setup-wizard-page'
			});
			const initState = getStateFromContext(wizardContext);
			updateState(initState);
			dispatch(SN_GW__REQUEST_CONNECTION_CREDENTIALS, { data: dataParam });
		},
		[SN_GW__REQUEST_CONNECTION_CREDENTIALS]: requestConnectionCredentials,
		[SN_GW__GET_CONNECTION_CREDENTIALS_SUCCEEDED]: ({
			action: { payload },
			updateState
		}) => {
			let crendentials = [];
			let selected_twilio = '';
			if (payload && payload.items && payload.items.length > 0) {
				crendentials = payload.items.map(item => {
					if (item.active == 'true') {
						selected_twilio = item.sys_id;
					}
					return {
						id: item.sys_id,
						label: item.$$displayValue
					};
				});
			}
			updateState({
				fetchCredentialsComplete: true,
				connectionCredentials: crendentials,
				twilio_connection: selected_twilio
			});
		},
		[SN_GW__GET_CONNECTION_CREDENTIALS_FAILED]: ({
			action: { payload },
			dispatch
		}) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		},
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action: {
				payload: { item, name },
				stopPropagation
			},
			updateState
		}) => {
			stopPropagation();
			updateState({ [name]: item.id });
		},
		['NOW_TEXTAREA#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			const { name, value } = payload;
			updateState({ [name]: value });
		}
	},
	behaviors: [createValidationBehavior(['sms_to', 'sms_from'])],
	styles
});
