import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { contextHelpers } from '@devsnc/guided-wizard-toolkit';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import styles from './styles.scss';
import '@servicenow/now-textarea';
import {
	SN_GW__REQUEST_TWITTER_CONNECTION,
	SN_GW__GET_TWITTER_CONNECTION_SUCCEEDED,
	SN_GW__GET_TWITTER_CONNECTION_FAILED,
	dataParm
} from './constants';
import '../../../components/sn-flow-connection-warning';
import spokesDataBehavior from '../../../behaviors/spokes-data';
import infinityIcon from '../../../assets/infinity-icon.png';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['spoke_connection', 'message_field']
});

const {
	constants: {
		INSTALLED_SPOKES_PROPERTY,
		MISSING_CONNECTIONS_PROPERTY,
		SPOKES_DATA_LOADING_PROPERTY
	},
	helpers: { fetchSpokes }
} = spokesDataBehavior;

export const requestAssignmentGroups = createHttpEffect(
	'/angular.do?sysparm_type=ref_list_data',
	{
		method: 'POST',
		successActionType: SN_GW__GET_TWITTER_CONNECTION_SUCCEEDED,
		errorActionType: SN_GW__GET_TWITTER_CONNECTION_FAILED,
		batch: false,
		dataParam: 'data'
	}
);

createCustomElement('sn-twitter-actions-wizard-page', {
	view: (state, { dispatch }) => {
		const {
			fetchConnectionCompleted,
			twitterConnections,
			spoke_connection,
			message_field,
			tableFields,
			properties: {
				wizardContext,
				[SPOKES_DATA_LOADING_PROPERTY]: spokesLoading,
				[INSTALLED_SPOKES_PROPERTY]: installedSpokes,
				[MISSING_CONNECTIONS_PROPERTY]: missingConnections
			}
		} = state;
		const contextFromState = getContextFromState(state, wizardContext);
		const twitterIcon =
			installedSpokes.length > 0 ? installedSpokes[0].logo : infinityIcon;

		return (
			<sn-guided-wizard-page
				max-width={wizardContext.maxWidth}
				running-header-text={t('BUILD A FLOW FROM TEMPLATE')}
				header-text={t("Let's define what happens when your flow runs")}
				onPrimaryClick={() => {
					dispatch('SN_GUIDED_WIZARD#CONTINUE', {
						...contextFromState,
						error: null
					});
				}}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onPrevious={() => {
					dispatch('SN_GUIDED_WIZARD#PREVIOUS', {
						...contextFromState,
						error: null
					});
				}}
				onclose={wizardContext.onClose}
				primary-disabled={!fetchConnectionCompleted}>
				{spokesLoading ? (
					<now-loader />
				) : (
					<Fragment>
						<div className="twilio-spoke-name">
							<sn-spoke-name
								spokeName={t('Twitter')}
								spokeAction={t('Send a Tweet')}
								icon={twitterIcon}
							/>
						</div>
						<sn-flow-connection-warning
							connections={missingConnections}
							installed-spokes={installedSpokes}
						/>
						<div className="inputs">
							<now-select
								label={t('Connection')}
								append-to-payload={{ name: 'spoke_connection' }}
								items={twitterConnections}
								search="contains"
								variant="tertiary"
								selectedItem={spoke_connection}
								value={spoke_connection}
								placeholder={t('Select Connection & Credential Alias')}
								style={{ width: '100%' }}
							/>
							<now-select
								append-to-payload={{ name: 'message_field' }}
								label={t('Message')}
								placeholder={t('Select field')}
								items={tableFields}
								search="contains"
								value={message_field}
								variant="tertiary"
								style={{ width: '100%' }}
							/>
						</div>
					</Fragment>
				)}
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		twitterConnections: [],
		fetchConnectionCompleted: false,
		spoke_connection: '',
		message_field: '',
		tableFields: []
	},
	properties: {
		wizardContext: { default: null },
		[INSTALLED_SPOKES_PROPERTY]: { default: [] },
		[MISSING_CONNECTIONS_PROPERTY]: { default: [] },
		[SPOKES_DATA_LOADING_PROPERTY]: { default: true }
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			const {
				app: { sys_id },
				templateId
			} = wizardContext;
			if (sys_id && templateId) {
				fetchSpokes(dispatch, sys_id, templateId);
			}

			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-twitter-actions-wizard-page'
			});
			const initState = getStateFromContext(wizardContext);
			let fields = [];
			var jsonFields = wizardContext.tableFields;
			if (jsonFields) {
				var keys = Object.keys(jsonFields);
				keys.forEach(function(field) {
					fields.push({
						id: jsonFields[field].name,
						label: jsonFields[field].label
					});
				});
				updateState({ tableFields: fields });
			}
			updateState(initState);
			dispatch(SN_GW__REQUEST_TWITTER_CONNECTION, { data: dataParm });
		},
		[SN_GW__REQUEST_TWITTER_CONNECTION]: requestAssignmentGroups,
		[SN_GW__GET_TWITTER_CONNECTION_SUCCEEDED]: ({
			action: { payload },
			updateState
		}) => {
			let twitter_connections = [];
			let selected_twitter = '';
			if (payload && payload.items && payload.items.length > 0) {
				payload.items.map(item => {
					twitter_connections.push({
						id: item.sys_id,
						label: item.$$displayValue
					});
					if (item.active == 'true') {
						selected_twitter = item.sys_id;
					}
				});
			}
			updateState({
				fetchConnectionCompleted: true,
				twitterConnections: twitter_connections,
				spoke_connection: selected_twitter
			});
		},
		[SN_GW__GET_TWITTER_CONNECTION_FAILED]: ({
			action: { payload },
			dispatch
		}) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action: {
				payload: { item, name },
				stopPropagation
			},
			updateState
		}) => {
			stopPropagation();
			updateState({ [name]: item.id });
		}
	},
	behaviors: [{ behavior: spokesDataBehavior.behavior }],
	styles
});
