import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import '@servicenow/now-alert';
import styles from './styles.scss';

function getMissingConnectionNames(missingConnections) {
	if (missingConnections.length === 0) {
		return '';
	}
	if (missingConnections.length === 1) {
		return missingConnections[0];
	}
	const lastConnection = missingConnections.splice(
		missingConnections.length - 1,
		1
	);
	return t('{0} and {1}', missingConnections.join(','), lastConnection[0]);
}

createCustomElement('sn-flow-connection-warning', {
	view: ({ properties: { connections, installedSpokes } }) => {
		if (installedSpokes.length === 0 || connections.length === 0) {
			return null;
		}

		const installedSpokesMap = installedSpokes.reduce((map, spoke) => {
			map[spoke.scope] = spoke;
			return map;
		}, {});

		const con = connections
			.filter(conn => installedSpokesMap[conn.scopeName])
			.map(conn => conn.spokeName);

		if (con.length === 0) {
			return null;
		}
		const missingConnections = getMissingConnectionNames(con);
		return (
			<now-alert
				status="warning"
				icon="info-circle-outline"
				header={t('There is no connection set up for {0}.', missingConnections)}
				content={t(
					'You can proceed with your flow, but you may need to contact your system administrator to set up the connection.'
				)}
				textLinkProps={{ label: t('Contact administrator'), href: 'mailto:' }}
			/>
		);
	},
	properties: {
		connections: {
			default: []
		},
		installedSpokes: {
			default: []
		}
	},
	styles
});
