import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { contextHelpers } from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import styles from './styles.scss';
import '@servicenow/now-textarea';
import {
	SN_GW__REQUEST_CONNECTION_CREDENTIALS,
	SN_GW__GET_CONNECTION_CREDENTIALS_SUCCEEDED,
	SN_GW__GET_CONNECTION_CREDENTIALS_FAILED,
	dataParm
} from './constants';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: [
		'slack_channel',
		'zoom_user_id',
		'email_to',
		'msteams_webhook_url',
		'slack_connection',
		'zoom_connection'
	]
});

export const requestConnectionCredentials = createHttpEffect(
	'/angular.do?sysparm_type=ref_list_data',
	{
		method: 'POST',
		successActionType: SN_GW__GET_CONNECTION_CREDENTIALS_SUCCEEDED,
		errorActionType: SN_GW__GET_CONNECTION_CREDENTIALS_FAILED,
		batch: false,
		dataParam: 'data'
	}
);

const zoom_spokes = {
	zoom: {
		name: t('Zoom'),
		action: t('Create a meeting')
	},
	slack: {
		name: t('Slack'),
		action: t('Send a message')
	},
	teams: {
		name: t('Microsoft Teams'),
		action: t('Send a message')
	},
	email: {
		name: t('Email'),
		action: t('Send an email')
	}
};

function renderSpokeNameIcon(source, spokeIcon) {
	if (!source) {
		return;
	}
	return (
		<div>
			<sn-spoke-name
				spokeName={zoom_spokes[source].name}
				spokeAction={zoom_spokes[source].action}
				icon={spokeIcon}
			/>
		</div>
	);
}

function renderSecondSetupPart(source, state) {
	const {
		slackConnections,
		slack_channel,
		email_to,
		msteams_webhook_url,
		properties: {
			wizardContext: { selectedSpokeIcon }
		}
	} = state;
	if (source == 'slack') {
		return (
			<div className="inputs">
				{renderSpokeNameIcon('slack', selectedSpokeIcon)}
				<div className="inputs">
					<now-select
						label={t('Connection')}
						append-to-payload={{ name: 'slack_connection' }}
						items={slackConnections}
						search="contains"
						variant="tertiary"
						selectedItem={state.slack_connection}
						placeholder={t('Select Connection & Credential Alias')}
						style={{ width: '100%' }}
					/>
					<now-input
						name="slack_channel"
						value={slack_channel}
						label={t('Slack Channel')}
						size="lg"
						autocomplete="off"
						style={{ width: '100%' }}
					/>
				</div>
			</div>
		);
	} else if (source == 'email') {
		return (
			<div className="inputs">
				{renderSpokeNameIcon('email', selectedSpokeIcon)}
				<div className="inputs">
					<now-input
						name="email_to"
						value={email_to}
						label={t('To')}
						size="lg"
						autocomplete="off"
						style={{ width: '100%' }}
					/>
				</div>
			</div>
		);
	} else if (source == 'teams') {
		return (
			<div className="inputs">
				{renderSpokeNameIcon('teams', selectedSpokeIcon)}
				<div className="inputs">
					<now-input-url
						name="msteams_webhook_url"
						append-to-payload={{ field: 'msteams_webhook_url' }}
						value={msteams_webhook_url}
						label={t('Webhook URL')}
						size="lg"
						autocomplete="off"
						style={{ width: '100%' }}
					/>
				</div>
			</div>
		);
	}
}

createCustomElement('sn-zoom-actions-wizard-page', {
	view: (state, { dispatch }) => {
		const {
			zoomConnections,
			zoom_connection,
			fetchCredentialsComplete,
			zoom_user_id,
			properties: { wizardContext }
		} = state;
		const {
			appData: { spoke_selection },
			zoomSpokeIcon
		} = wizardContext;
		const contextFromState = getContextFromState(state, wizardContext);
		return (
			<sn-guided-wizard-page
				max-width={wizardContext.maxWidth}
				running-header-text={t('BUILD A FLOW FROM TEMPLATE')}
				header-text={t("Let's define what happens when your flow runs")}
				onPrimaryClick={() => {
					dispatch('SN_GUIDED_WIZARD#CONTINUE', {
						...contextFromState,
						error: null
					});
				}}
				onPrevious={() => {
					dispatch('SN_GUIDED_WIZARD#PREVIOUS', {
						...contextFromState,
						error: null
					});
				}}
				onclose={wizardContext.onClose}
				primary-label="Done"
				primary-disabled={!fetchCredentialsComplete}>
				{renderSpokeNameIcon('zoom', zoomSpokeIcon)}
				<div className="inputs">
					<now-select
						label={t('Connection')}
						append-to-payload={{ name: 'zoom_connection' }}
						items={zoomConnections}
						search="contains"
						variant="tertiary"
						selectedItem={zoom_connection}
						placeholder={t('Select Connection & Credential Alias')}
						style={{ width: '100%' }}
					/>
					<now-input
						type="text"
						label={t('User ID')}
						placeholder={t('Enter User ID')}
						value={zoom_user_id}
						name="zoom_user_id"
						style={{ width: '100%' }}
						autocomplete="off"
					/>
				</div>
				{renderSecondSetupPart(spoke_selection, state)}
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		zoomConnections: [],
		fetchCredentialsComplete: false,
		zoom_connection: '',
		slack_connection: '',
		zoom_user_id: '',
		slack_channel: '',
		slackConnections: [],
		email_to: '',
		msteams_webhook_url: '',
		isFormValid: false
	},
	properties: {
		wizardContext: { default: null }
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-zoom-actions-wizard-page'
			});
			const initState = getStateFromContext(wizardContext);
			updateState(initState);
			dispatch(SN_GW__REQUEST_CONNECTION_CREDENTIALS, { data: dataParm });
		},
		[SN_GW__REQUEST_CONNECTION_CREDENTIALS]: requestConnectionCredentials,
		[SN_GW__GET_CONNECTION_CREDENTIALS_SUCCEEDED]: ({
			action: { payload },
			updateState
		}) => {
			let zoomCrendentials = [];
			let slackCredentials = [];
			let selected_zoom = '';
			let selected_slack = '';
			if (payload && payload.items && payload.items.length > 0) {
				payload.items.map(item => {
					if (item.connection_alias == 'sn_zoom_spoke.Zoom') {
						zoomCrendentials.push({
							id: item.sys_id,
							label: item.$$displayValue
						});
						if (item.active == 'true') {
							selected_zoom = item.sys_id;
						}
					} else if (item.connection_alias == 'sn_slack_ah_v2.Slack') {
						slackCredentials.push({
							id: item.sys_id,
							label: item.$$displayValue
						});
						if (item.active == 'true') {
							selected_slack = item.sys_id;
						}
					}
				});
			}
			updateState({
				fetchCredentialsComplete: true,
				zoomConnections: zoomCrendentials,
				slackConnections: slackCredentials,
				zoom_connection: selected_zoom,
				slack_connection: selected_slack
			});
		},
		[SN_GW__GET_CONNECTION_CREDENTIALS_FAILED]: ({
			action: { payload },
			dispatch
		}) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		},
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action: {
				payload: { item, name },
				stopPropagation
			},
			updateState
		}) => {
			stopPropagation();
			updateState({ [name]: item.id });
		},
		['NOW_INPUT_URL#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			const { name, value } = payload;
			updateState({ [name]: value });
		}
	},
	behaviors: [],
	styles
});
