import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-input';
import '@servicenow/now-label-value';
import '@servicenow/now-textarea';
import {
	contextHelpers,
	validationBehavior
} from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
import '../../../components/sn-spoke-name';
import styles from './styles.scss';

const {
	helpers: { createValidationBehavior, returnFormValidState }
} = validationBehavior;

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['msteams_webhook_url', 'msteams_message', 'msteams_title']
});

createCustomElement('sn-reminder-teams-action-setup-wizard-page', {
	view: (state, { dispatch }) => {
		const {
			isFormValid,
			msteams_webhook_url,
			msteams_title,
			msteams_message,
			properties: { wizardContext }
		} = state;
		return (
			<sn-guided-wizard-page
				max-width={wizardContext.maxWidth}
				running-header-text={t('BUILD A FLOW FROM TEMPLATE')}
				header-text={t("Let's define what happens when your flow runs")}
				primary-disabled={!isFormValid}
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#CONTINUE', {
						...contextFromState,
						error: null
					});
				}}
				onPrevious={() => {
					// Can pass in a newContext on the previous action as well
					const newContext = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#PREVIOUS', newContext);
				}}
				onclose={wizardContext.onClose}
				primary-label="Done">
				<sn-spoke-name
					spokeName={t('Teams')}
					spokeAction={t('Post a message')}
					icon={wizardContext.selectedSpokeIcon}
				/>
				<div className="inputs">
					<now-input-url
						name="msteams_webhook_url"
						append-to-payload={{ field: 'msteams_webhook_url' }}
						value={msteams_webhook_url}
						label={t('Webhook URL')}
						size="lg"
						autocomplete="off"
						style={{ width: '100%' }}
						required={true}
					/>
					<now-input
						name="msteams_title"
						append-to-payload={{ field: 'msteams_title' }}
						value={msteams_title}
						label={t('Title')}
						size="lg"
						autocomplete="off"
						style={{ width: '100%' }}
						required={true}
					/>
					<div className="flow-action-form-fields">
						<now-textarea
							type="text"
							label={t('Message')}
							style={{ width: '100%' }}
							value={msteams_message}
							name="msteams_message"
							resize="none"
							rows="5"
							required={true}
							append-to-payload={{ field: 'msteams_message' }}
						/>
					</div>
				</div>
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {}
	},
	initialState: {
		isFormValid: false,
		msteams_webhook_url: '',
		msteams_title: '',
		msteams_message: ''
	},
	transformState: state => {
		const isFormValid = returnFormValidState(state);
		return { ...state, isFormValid };
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			dispatch,
			properties: { wizardContext },
			updateState
		}) => {
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-reminder-action-setup-wizard-page'
			});
			const initState = getStateFromContext(wizardContext);
			updateState(initState);
		},
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		},
		['NOW_INPUT_URL#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			const { name, value } = payload;
			updateState({ [name]: value });
		},
		['NOW_TEXTAREA#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			const { name, value } = payload;
			updateState({ [name]: value });
		}
	},
	behaviors: [createValidationBehavior(['msteams_title'])],
	styles
});
