import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import {
	contextHelpers,
	validationBehavior
} from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
import styles from './styles.scss';
import '@servicenow/now-textarea';
import { WIZARD_PAGES, ACTIONS } from '../../constants';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['email_to', 'email_subject', 'email_body']
});

const { EMAIL_ACTION_SETUP_PAGE, ACTION_SETUP_PAGE } = WIZARD_PAGES;
const {
	GUIDED_WIZARD_CONTINUE,
	GUIDED_WIZARD_PREVIOUS,
	GUIDED_WIZARD_CANCEL,
	STEPPER_BEHAVIOR_PAGE_SET
} = ACTIONS;

const {
	helpers: { createValidationBehavior, returnFormValidState }
} = validationBehavior;

createCustomElement(EMAIL_ACTION_SETUP_PAGE, {
	view: (state, dispatch) => {
		const {
			isFormValid,
			email_to,
			email_subject,
			email_body,
			properties: { wizardContext }
		} = state;

		return (
			<sn-guided-wizard-page
				primaryDisabled={!isFormValid}
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_CONTINUE, {
						...contextFromState,
						error: null
					});
				}}
				onPrevious={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_PREVIOUS, {
						...contextFromState,
						error: null
					});
				}}
				onSecondaryClick={() => dispatch(GUIDED_WIZARD_CANCEL)}
				onClose={wizardContext.onClose}
				runningHeaderText={t('BUILD A FLOW FROM A TEMPLATE')}
				maxWidth={wizardContext.maxWidth}>
				<sn-spoke-name
					spokeName={t('Email')}
					spokeAction={t('Send Email')}
					icon={wizardContext.selectedSpokeIcon}
				/>
				<div className="flow-action-form-fields">
					<now-input
						type="email"
						label={t('To')}
						placeholder={t('Enter Email address')}
						value={email_to}
						name="email_to"
						required={true}
						multiple={true}
						style={{ width: '100%' }}
						append-to-payload={{ field: 'email_to' }}
					/>
					<now-input
						type="text"
						label={t('Subject')}
						placeholder={t('Enter Email subject')}
						style={{ width: '100%' }}
						value={email_subject}
						required={true}
						name="email_subject"
						append-to-payload={{ field: 'email_subject' }}
					/>
					<now-textarea
						type="text"
						label={t('Body')}
						placeholder={t('Enter Email body')}
						style={{ width: '100%' }}
						value={email_body}
						rows="3"
						append-to-payload={{ field: 'email_body' }}
					/>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		email_to: '',
		email_subject: '',
		email_body: '',
		isFormValid: false
	},
	properties: {
		wizardContext: { default: null }
	},
	transformState: state => {
		const isFormValid = returnFormValidState(state);
		return { ...state, isFormValid };
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			dispatch(STEPPER_BEHAVIOR_PAGE_SET, {
				page: ACTION_SETUP_PAGE
			});

			const initState = getStateFromContext(wizardContext);
			updateState(initState);
		},
		['NOW_INPUT#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		},
		['NOW_TEXTAREA#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		}
	},
	behaviors: [createValidationBehavior(['email_to', 'email_subject'])],
	styles
});
