import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-select';
import '@servicenow/now-loader';
import { contextHelpers } from '@devsnc/guided-wizard-toolkit';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import styles from './styles.scss';
import { t } from 'sn-translate';

import {
	SN_GW__REQUEST_FLOW_TABLES,
	SN_GW__REQUEST_TABLES_STARTED,
	SN_GW__GET_FLOW_TABLES_SUCCEEDED,
	SN_GW__GET_FLOW_TABLES_FAILED,
	SN_GW__REQUEST_FLOW_STATE,
	SN_GW__GET_FLOW_STATE_SUCCEEDED,
	SN_GW__GET_FLOW_STATE_FAILED,
	SN_GW__REQUEST_STATE_STARTED,
	queryParam
} from './constants';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['trigger_table', 'record_state']
});

export const fetchTables = createHttpEffect(
	'/api/now/processflow/table/schema?',
	{
		method: 'GET',
		startActionType: SN_GW__REQUEST_TABLES_STARTED,
		successActionType: SN_GW__GET_FLOW_TABLES_SUCCEEDED,
		errorActionType: SN_GW__GET_FLOW_TABLES_FAILED,
		batch: false,
		queryParams: 'query',
		pathParams: []
	}
);
// var table = 'incident';
let fetchState = createHttpEffect('/api/now/ui/meta/:tableName', {
	method: 'GET',
	startActionType: SN_GW__REQUEST_STATE_STARTED,
	successActionType: SN_GW__GET_FLOW_STATE_SUCCEEDED,
	errorActionType: SN_GW__GET_FLOW_STATE_FAILED,
	batch: false,
	queryParams: '',
	pathParams: ['tableName']
});

createCustomElement('sn-twitter-trigger-select-wizard-page', {
	view: (state, { dispatch }) => {
		const {
			allTables,
			trigger_table,
			isFetching,
			isFetchingState,
			allStates,
			record_state,
			tableFields,
			properties: { wizardContext }
		} = state;
		const contextFromState = getContextFromState(state, wizardContext);
		function onPrimaryClick() {
			dispatch('SN_GUIDED_WIZARD#CONTINUE', {
				tableFields: tableFields,
				...contextFromState,
				error: null
			});
		}

		return (
			<sn-guided-wizard-page
				onPrimaryClick={onPrimaryClick}
				primary-disabled={!trigger_table}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				running-header-text={t('BUILD A FLOW FROM A TEMPLATE')}
				header-text={t('Choose a table to trigger your flow')}
				subheader-text={t(
					'When a record is updated for this table, a Twitter post will be created.'
				)}
				max-width={wizardContext.maxWidth}>
				<div className="inputs">
					{isFetching === false && allTables.length > 0 ? (
						<now-select
							search="contains"
							label={t('Table')}
							append-to-payload={{ name: 'trigger_table' }}
							items={allTables}
							selectedItem={trigger_table}
							icon=""
							placeholder=""
							size="md"
							variant="tertiary"
							select="single"
							panelFitProps={{}}
							configAria={{}}
							style={{ width: '100%' }}
						/>
					) : (
						<now-loader />
					)}
				</div>
				<br />
				<div className="inputs">
					{isFetchingState === false && trigger_table ? (
						<now-select
							search="contains"
							label={t('State')}
							append-to-payload={{ name: 'record_state' }}
							items={allStates}
							selectedItem={record_state}
							icon=""
							placeholder=""
							size="md"
							variant="tertiary"
							select="single"
							panelFitProps={{}}
							configAria={{}}
							style={{ width: '100%' }}
						/>
					) : (
						<now-loader />
					)}
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		allTables: [],
		trigger_table: 'incident',
		allStates: [],
		isFetching: false,
		isFetchingState: false,
		record_state: '',
		tableFields: []
	},
	properties: {
		wizardContext: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			// Send to the current page to the stepper behavior
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-twitter-trigger-select-wizard-page'
			});
			dispatch(SN_GW__REQUEST_FLOW_TABLES, { query: queryParam });
			dispatch(SN_GW__REQUEST_FLOW_STATE, {
				tableName: 'incident'
			});
			// Populate state with wizardContext so inputs are persisted when
			// coming back to this page
			const initState = getStateFromContext(wizardContext);
			updateState(initState);
		},
		[SN_GW__REQUEST_TABLES_STARTED]: ({ updateState }) =>
			updateState({
				isFetching: true
			}),
		[SN_GW__REQUEST_FLOW_TABLES]: fetchTables,
		[SN_GW__GET_FLOW_TABLES_SUCCEEDED]: ({
			action: { payload },
			updateState
		}) => {
			let tableNames = [];
			if (payload && payload.result && payload.result.length > 0) {
				tableNames = payload.result.map(item => {
					return {
						id: item.value,
						label: item.label + ' [' + item.value + ']'
					};
				});
			}
			updateState({ allTables: tableNames, isFetching: false });
		},
		[SN_GW__GET_FLOW_TABLES_FAILED]: ({ action: { payload }, dispatch }) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		},
		[SN_GW__REQUEST_STATE_STARTED]: ({ updateState }) =>
			updateState({
				isFetchingState: true
			}),
		[SN_GW__GET_FLOW_STATE_FAILED]: () => {
			//error
		},
		[SN_GW__GET_FLOW_STATE_SUCCEEDED]: ({
			action: { payload },
			updateState
		}) => {
			let stateVals = [];
			let fields = [];
			if (payload && payload.result) {
				fields = payload.result.columns;
				if (
					payload.result.columns &&
					payload.result.columns.state &&
					payload.result.columns.state.choices &&
					payload.result.columns.state.choices.length > 0
				) {
					stateVals = payload.result.columns.state.choices.map(item => {
						return {
							id: item.value,
							label: item.label
						};
					});
				}
			}
			updateState({
				allStates: stateVals,
				isFetchingState: false,
				tableFields: fields
			});
		},
		[SN_GW__REQUEST_FLOW_STATE]: fetchState,
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action: { payload, stopPropagation },
			updateState,
			dispatch
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
			if (name == 'trigger_table') {
				dispatch(SN_GW__REQUEST_FLOW_STATE, { tableName: value });
			}
		}
	},
	styles
});
