import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import {
	contextHelpers,
	validationBehavior
} from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
import styles from './styles.scss';
import '@servicenow/now-textarea';
import { WIZARD_PAGES, ACTIONS } from '../../constants';
import '../../../components/sn-spoke-name';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['slack_channel', 'slack_message']
});

const { SLACK_ACTION_SETUP_PAGE, ACTION_SETUP_PAGE } = WIZARD_PAGES;
const {
	GUIDED_WIZARD_CONTINUE,
	GUIDED_WIZARD_PREVIOUS,
	GUIDED_WIZARD_CANCEL,
	STEPPER_BEHAVIOR_PAGE_SET
} = ACTIONS;

const {
	helpers: { createValidationBehavior, returnFormValidState }
} = validationBehavior;

createCustomElement(SLACK_ACTION_SETUP_PAGE, {
	view: (state, dispatch) => {
		const {
			isFormValid,
			slack_channel,
			slack_message,
			properties: { wizardContext }
		} = state;

		return (
			<sn-guided-wizard-page
				primaryDisabled={!isFormValid || !slack_message}
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_CONTINUE, {
						...contextFromState,
						error: null
					});
				}}
				onPrevious={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_PREVIOUS, {
						...contextFromState,
						error: null
					});
				}}
				onSecondaryClick={() => dispatch(GUIDED_WIZARD_CANCEL)}
				onClose={wizardContext.onClose}
				runningHeaderText={t('BUILD A FLOW FROM A TEMPLATE')}
				max-width={wizardContext.maxWidth}>
				<sn-spoke-name
					spokeName={t('Slack')}
					spokeAction={t('Post a message')}
					icon={wizardContext.selectedSpokeIcon}
				/>
				<div className="flow-action-form-fields">
					<now-input
						type="text"
						label={t('Channel')}
						placeholder={t('Enter channel ID')}
						value={slack_channel}
						name="slack_channel"
						required={true}
						style={{ width: '100%' }}
						append-to-payload={{ field: 'slack_channel' }}
					/>
					<now-textarea
						type="text"
						label={t('Message')}
						placeholder={t('Enter message')}
						style={{ width: '100%' }}
						value={slack_message}
						name="slack_message"
						rows="3"
						required={true}
						append-to-payload={{ field: 'slack_message' }}
					/>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		slack_channel: '',
		slack_message: '',
		isFormValid: false
	},
	properties: {
		wizardContext: { default: null }
	},
	transformState: state => {
		const isFormValid = returnFormValidState(state);
		return { ...state, isFormValid };
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			dispatch(STEPPER_BEHAVIOR_PAGE_SET, {
				page: ACTION_SETUP_PAGE
			});

			const initState = getStateFromContext(wizardContext);
			updateState(initState);
		},
		['NOW_INPUT#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		},
		['NOW_TEXTAREA#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		}
	},
	behaviors: [createValidationBehavior(['slack_channel'])],
	styles
});
