import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-input';
import '@servicenow/now-select';
import {
	contextHelpers,
	validationBehavior
} from '@devsnc/guided-wizard-toolkit';
import styles from './styles.scss';
import { t } from 'sn-translate';

const {
	helpers: { createValidationBehavior, returnFormValidState }
} = validationBehavior;

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['weekly_day', 'weekly_hour', 'weekly_minute', 'weekly_second']
});

createCustomElement('sn-reminder-trigger-wizard-page', {
	view: (state, { dispatch }) => {
		const {
			isFormValid,
			weekly_day,
			weekly_hour,
			weekly_minute,
			weekly_second,
			properties: { wizardContext }
		} = state;
		function onPrimaryClick() {
			const contextFromState = getContextFromState(state, wizardContext);
			dispatch('SN_GUIDED_WIZARD#CONTINUE', {
				...contextFromState,
				error: null
			});
		}
		const items = [
			{ id: 'monday', label: t('Monday') },
			{ id: 'tuesday', label: t('Tuesday') },
			{ id: 'wednesday', label: t('Wednesday') },
			{ id: 'thursday', label: t('Thursday') },
			{ id: 'friday', label: t('Friday') },
			{ id: 'saturday', label: t('Saturday') },
			{ id: 'sunday', label: t('Sunday') }
		];
		return (
			<sn-guided-wizard-page
				onPrimaryClick={onPrimaryClick}
				primary-disabled={!isFormValid}
				onSecondaryClick={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				running-header-text={t('BUILD A FLOW FROM A TEMPLATE')}
				header-text={t('Set a schedule to trigger your flow')}
				max-width={wizardContext.maxWidth}>
				<div className="inputs">
					<now-select
						label={t('Day of the week')}
						append-to-payload={{ name: 'weekly_day' }}
						items={items}
						selectedItem={weekly_day}
						placeholder="Select a day"
						size="md"
						variant="tertiary"
						select="single"
						panelFitProps={{}}
						configAria={{}}
						style={{ width: '100%' }}
						required
					/>
					<br />
					<div className="time-inputs">
						<span>
							<now-input
								type="number"
								name="weekly_hour"
								value={weekly_hour}
								placeholder="00"
								label={t('Hours')}
								size="sm"
								autocomplete="off"
								max={23}
								min={0}
								required
							/>
						</span>
						<span>
							<now-input
								type="number"
								name="weekly_minute"
								value={weekly_minute}
								placeholder="00"
								label={t('Minutes')}
								size="sm"
								autocomplete="off"
								max={59}
								min={0}
								required
							/>
						</span>
						<span>
							<now-input
								type="number"
								name="weekly_second"
								value={weekly_second}
								placeholder="00"
								label={t('Seconds')}
								size="sm"
								autocomplete="off"
								max={59}
								min={0}
								required
							/>
						</span>
					</div>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		isFormValid: false,
		weekly_day: '',
		weekly_hour: '',
		weekly_minute: '',
		weekly_second: ''
	},
	transformState: state => {
		const isFormValid = returnFormValidState(state);
		return { ...state, isFormValid };
	},
	properties: {
		wizardContext: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			// Send to the current page to the stepper behavior
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-reminder-trigger-wizard-page'
			});

			// Populate state with wizardContext so inputs are persisted when
			// coming back to this page
			const initState = getStateFromContext(wizardContext);
			updateState(initState);
		},
		// Actions from now-input
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action: {
				payload: { item, name },
				stopPropagation
			},
			updateState
		}) => {
			updateState({ [name]: item.id });
			stopPropagation();
		}
	},
	behaviors: [
		createValidationBehavior(['weekly_hour', 'weekly_minute', 'weekly_second'])
	],
	styles
});
