import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { contextHelpers } from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import styles from './styles.scss';
import '@servicenow/now-textarea';
import {
	SN_GW__REQUEST_ASSIGNMENT_GROUP,
	SN_GW__GET_ASSIGNMENT_GROUP_SUCCEEDED,
	SN_GW__GET_ASSIGNMENT_GROUP_FAILED,
	dataParm
} from './constants';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: ['assignment_group_vip', 'assignment_group_others']
});

export const requestAssignmentGroups = createHttpEffect(
	'/angular.do?sysparm_type=ref_list_data',
	{
		method: 'POST',
		successActionType: SN_GW__GET_ASSIGNMENT_GROUP_SUCCEEDED,
		errorActionType: SN_GW__GET_ASSIGNMENT_GROUP_FAILED,
		batch: false,
		dataParam: 'data'
	}
);

createCustomElement('sn-vip-actions-wizard-page', {
	view: (state, { dispatch }) => {
		const {
			fetchGroupCompleted,
			assignmentGroups,
			assignment_group_vip,
			assignment_group_others,
			properties: { wizardContext }
		} = state;
		const contextFromState = getContextFromState(state, wizardContext);
		return (
			<sn-guided-wizard-page
				max-width={wizardContext.maxWidth}
				running-header-text={t('BUILD A FLOW FROM TEMPLATE')}
				header-text={t("Let's define what happens when your flow runs")}
				onPrimaryClick={() => {
					dispatch('SN_GUIDED_WIZARD#CONTINUE', {
						...contextFromState,
						error: null
					});
				}}
				onPrevious={() => {
					dispatch('SN_GUIDED_WIZARD#PREVIOUS', {
						...contextFromState,
						error: null
					});
				}}
				onclose={wizardContext.onClose}
				primary-label="Done"
				primary-disabled={!fetchGroupCompleted}>
				<br />
				<div className="inputs">
					<now-select
						label={t('Select assignment group for VIPs ')}
						selectedItem={assignment_group_vip}
						append-to-payload={{ name: 'assignment_group_vip' }}
						items={assignmentGroups}
						search="contains"
						variant="tertiary"
						placeholder={t('Select assignment group for VIPs ')}
						style={{ width: '100%' }}
					/>
					<now-select
						label={t('Select assignment group for all other callers')}
						selectedItem={assignment_group_others}
						append-to-payload={{ name: 'assignment_group_others' }}
						items={assignmentGroups}
						search="contains"
						variant="tertiary"
						placeholder={t('Select assignment group for all other callers')}
						style={{ width: '100%' }}
					/>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		assignmentGroups: [],
		fetchGroupCompleted: false,
		assignment_group_vip: {},
		assignment_group_others: {}
	},
	properties: {
		wizardContext: { default: null }
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-vip-actions-wizard-page'
			});
			const initState = getStateFromContext(wizardContext);
			updateState(initState);
			dispatch(SN_GW__REQUEST_ASSIGNMENT_GROUP, { data: dataParm });
		},
		[SN_GW__REQUEST_ASSIGNMENT_GROUP]: requestAssignmentGroups,
		[SN_GW__GET_ASSIGNMENT_GROUP_SUCCEEDED]: ({
			action: { payload },
			updateState
		}) => {
			let assignment_group = [];
			if (payload && payload.items && payload.items.length > 0) {
				payload.items.map(item => {
					assignment_group.push({
						label: item.name,
						id: item.sys_id
					});
				});
			}
			updateState({
				fetchGroupCompleted: true,
				assignmentGroups: assignment_group
			});
		},
		[SN_GW__GET_ASSIGNMENT_GROUP_FAILED]: ({
			action: { payload },
			dispatch
		}) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		},
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
		},
		['NOW_SELECT#SELECTED_ITEM_SET']: ({
			action: {
				payload: { item, name },
				stopPropagation
			},
			updateState
		}) => {
			stopPropagation();
			updateState({ [name]: item.id });
		}
	},
	behaviors: [],
	styles
});
