// UI-Framework imports
import { createCustomElement } from '@servicenow/ui-core';
// sn-guided-wizard-toolkit components imports
import { getCurrentPage, stepperBehavior } from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
// Pages
import './pages/sn-reminder-trigger-wizard-page';
import './pages/sn-reminder-spoke-select-wizard-page';
import './pages/sn-reminder-slack-action-setup-wizard-page';
import './pages/sn-reminder-gmail-action-setup-wizard-page';
import './pages/sn-reminder-teams-action-setup-wizard-page';
import './pages/sn-reminder-sms-action-setup-wizard-page';
import './pages/sn-reminder-define-flow-wizard-page';
import '../components/sn-flow-summary-wizard-page';
// styles
import styles from './styles.scss';
import { FLOW_PAGE_TEXT } from '../helpers/flow-logic-naming-constants';

// Create variables for each page with the tagname as the value
const TRIGGER_PAGE = 'sn-reminder-trigger-wizard-page';
const SPOKE_PAGE = 'sn-reminder-spoke-select-wizard-page';
const ACTION_SETUP_PAGE = 'sn-reminder-action-setup-wizard-page';
const SLACK_ACTION_SETUP_PAGE = 'sn-reminder-slack-action-setup-wizard-page';
const TEAMS_ACTION_SETUP_PAGE = 'sn-reminder-teams-action-setup-wizard-page';
const GMAIL_ACTION_SETUP_PAGE = 'sn-reminder-gmail-action-setup-wizard-page';
const SMS_ACTION_SETUP_PAGE = 'sn-reminder-sms-action-setup-wizard-page';
const DEFINE_FLOW_PAGE = 'sn-reminder-define-flow-wizard-page';
const SUMMARY_PAGE = 'sn-flow-summary-wizard-page';

const pageMap = {
	slack: SLACK_ACTION_SETUP_PAGE,
	teams: TEAMS_ACTION_SETUP_PAGE,
	gmail: GMAIL_ACTION_SETUP_PAGE,
	twilio: SMS_ACTION_SETUP_PAGE
};

// Create flowDefinition, assign continue/previous for each page and define startPage
const fdWorkspaceReminderWizard = {
	startPage: TRIGGER_PAGE,
	pages: {
		[TRIGGER_PAGE]: {
			continue: () => SPOKE_PAGE
		},
		[SPOKE_PAGE]: {
			continue: data => pageMap[data.appData.spoke_selection],
			previous: () => TRIGGER_PAGE
		},
		[SLACK_ACTION_SETUP_PAGE]: {
			continue: () => DEFINE_FLOW_PAGE,
			previous: () => SPOKE_PAGE
		},
		[TEAMS_ACTION_SETUP_PAGE]: {
			continue: () => DEFINE_FLOW_PAGE,
			previous: () => SPOKE_PAGE
		},
		[GMAIL_ACTION_SETUP_PAGE]: {
			continue: () => DEFINE_FLOW_PAGE,
			previous: () => SPOKE_PAGE
		},
		[SMS_ACTION_SETUP_PAGE]: {
			continue: () => DEFINE_FLOW_PAGE,
			previous: () => SPOKE_PAGE
		},
		[DEFINE_FLOW_PAGE]: {
			continue: () => SUMMARY_PAGE,
			previous: data => pageMap[data.appData['spoke_selection']]
		},
		[SUMMARY_PAGE]: {
			previous: () => DEFINE_FLOW_PAGE
		}
	}
};

// Create object with tagname of page as the key and label for stepper as value
// Pass this in to the stepperBehavior behavior options as the `config` property
// This will return the updated stepperItems array to pass to sn-guided-wizard
const STEPPER_CONFIG = {
	[TRIGGER_PAGE]: t('Define trigger'),
	[SPOKE_PAGE]: t('Select spokes'),
	[ACTION_SETUP_PAGE]: t('Set up actions'),
	[DEFINE_FLOW_PAGE]: FLOW_PAGE_TEXT,
	[SUMMARY_PAGE]: t('Summary')
};

// Create wizard element
// This will be the element that is called from the template
// Make sure to include this in now-ui.json with innerComponents
createCustomElement('sn-reminder-for-workspace-flow-wizard', {
	view: ({ properties: { wizardContext, stepperItems, templateId, app } }) => {
		// Create element from current page set in sn-guided-wizard
		// Note: Must be capitalized in order for the variable component to render
		const PageComponent = getCurrentPage(wizardContext);
		return (
			<sn-guided-wizard
				// Always set a component-id
				component-id="fdWorkspaceReminderWizard"
				// Flow definition from above
				flow-definition={fdWorkspaceReminderWizard}
				// Stepper items from behavior
				stepper-items={stepperItems}
				// Define initial context
				// Not required but it's nice to define what forms you may
				// have to fill out during a wizard here and set any recurring values
				// here like maxWidth
				initial-context={{
					maxWidth: '620px',
					templateId,
					appData: {
						flow_scope_sys_id: app.sys_id
					},
					app
				}}>
				{/** Render current page and be sure to provide it wizardContext */}
				<PageComponent wizard-context={wizardContext} />
			</sn-guided-wizard>
		);
	},
	properties: {
		// wizardContext is the main data store for a wizard, always include
		// this property selector in the root of a wizard
		wizardContext: { default: '@fdWorkspaceReminderWizard/wizardContext' },
		// stepperItems property selector populated by the stepperBehavior
		stepperItems: { default: '@fdWorkspaceReminderWizard/stepperItems' },
		templateId: { default: 'df5d5859ff36101088e94ee9453bf1da' }, // Post a weekly reminder to a workspace
		app: { default: {} }
	},
	behaviors: [
		{ behavior: stepperBehavior.behavior, options: { config: STEPPER_CONFIG } }
	],
	actionHandlers: {},
	styles
});
