import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { contextHelpers } from '@devsnc/guided-wizard-toolkit';
import { t } from 'sn-translate';
import styles from './styles.scss';
import '@servicenow/now-textarea';
import { WIZARD_PAGES, ACTIONS } from '../../constants';

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'appData',
	keys: [
		'msteams_webhook_url',
		'msteams_title',
		'msteams_message',
		'isValidURL'
	]
});

const { TEAMS_ACTION_SETUP_PAGE, ACTION_SETUP_PAGE } = WIZARD_PAGES;
const {
	GUIDED_WIZARD_CONTINUE,
	GUIDED_WIZARD_PREVIOUS,
	GUIDED_WIZARD_CANCEL,
	STEPPER_BEHAVIOR_PAGE_SET
} = ACTIONS;

createCustomElement(TEAMS_ACTION_SETUP_PAGE, {
	view: (state, dispatch) => {
		const {
			isValidURL,
			msteams_webhook_url,
			msteams_title,
			msteams_message,
			properties: { wizardContext }
		} = state;

		return (
			<sn-guided-wizard-page
				primaryDisabled={!isValidURL}
				onPrimaryClick={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_CONTINUE, {
						...contextFromState,
						error: null
					});
				}}
				onPrevious={() => {
					const contextFromState = getContextFromState(state, wizardContext);
					dispatch(GUIDED_WIZARD_PREVIOUS, {
						...contextFromState,
						error: null
					});
				}}
				onSecondaryClick={() => dispatch(GUIDED_WIZARD_CANCEL)}
				onClose={wizardContext.onClose}
				runningHeaderText={t('BUILD A FLOW FROM A TEMPLATE')}
				maxWidth={wizardContext.maxWidth}>
				<sn-spoke-name
					spokeName={t('Microsoft Teams')}
					spokeAction={t('Post a message')}
					icon={wizardContext.selectedSpokeIcon}
				/>
				<div className="flow-action-form-fields">
					<now-input-url
						label={t('Webhook URL')}
						placeholder={t('Enter webhook URL')}
						value={msteams_webhook_url}
						name="msteams_webhook_url"
						required={true}
						style={{ width: '100%' }}
						append-to-payload={{ field: 'msteams_webhook_url' }}
					/>
					<now-input
						type="text"
						label={t('Title')}
						placeholder={t('Enter title')}
						style={{ width: '100%' }}
						value={msteams_title}
						append-to-payload={{ field: 'msteams_title' }}
					/>
					<now-textarea
						type="text"
						label={t('Body')}
						placeholder={t('Enter message')}
						style={{ width: '100%' }}
						value={msteams_message}
						rows="3"
						append-to-payload={{ field: 'msteams_message' }}
					/>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		msteams_webhook_url: '',
		msteams_title: '',
		msteams_message: '',
		isValidURL: false
	},
	properties: {
		wizardContext: { default: null }
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties: { wizardContext },
			dispatch,
			updateState
		}) => {
			dispatch(STEPPER_BEHAVIOR_PAGE_SET, {
				page: ACTION_SETUP_PAGE
			});

			const initState = getStateFromContext(wizardContext);
			updateState(initState);
		},
		['NOW_INPUT_URL#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		},
		['NOW_INPUT_URL#INVALID_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { value } = payload;
			updateState({ isValidURL: !value });
		},
		['NOW_INPUT#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		},
		['NOW_TEXTAREA#VALUE_SET']: ({
			action: { payload, stopPropagation },
			updateState
		}) => {
			stopPropagation();
			let { field, value } = payload;
			updateState({ [field]: value });
		}
	},
	styles
});
